@import '../variables';

.container-full {
    height: calc(100vh - 75px);
    width: 100%;
    @media(max-width: map-get($grid-breakpoints, 'md')) {
        height: calc(100vh - 60px); /* 60 = 60px (header) */
    }
}

.secondary-nav ~ .container .container-full, .secondary-nav ~ .container-fluid .container-full {
    height: calc(100vh - 135px);
    @media(max-width: map-get($grid-breakpoints, 'md')) {
        height: calc(100vh - 120px); /* 120 = 60px (header) + 60px (secondary nav) */
    }
}

.alert:not(.alert-fixed) ~ .secondary-nav ~ .container-full {
    height: calc(100vh - 180);
    @media(max-width: map-get($grid-breakpoints, 'md')) {
        height: calc(100vh - 165px); /* 165 = 60px (header) + 60px (secondary nav) + 45px (alert) */
    }
}

//remove negative margin from a row directly on top of a container-full
.container-full div .row {
    margin: 0;
}