@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.callout {
    display: flex;
    flex-direction: row;
    margin-bottom: 16px;

    .callout-icon {
        display: flex;
        flex: 0 0 auto;
        justify-content: center;
        padding: 16px 0;
        width: 54px;
        .fa {
            font-size: 24px;
            line-height: 24px;
        }
    }

    .callout-body {
        align-content: center;
        display: flex;
        font-family: $font-family-light;
        font-size: 16px;
        line-height: 24px;
        padding: 16px 20px;
        white-space: normal;
        code {
            background-color: $seashell;
            font-size: 11px;
            line-height: 12px;
        }
    }

    @each $name, $colors in $message-colors {
        &-#{$name} {
            @include themify($themes) {
                $background-color: themed-component(#{$name + '-background-color'}, 'help');
                $color: test-contrast($background-color);
                //$icon-background-color: themed-component(#{$name + '-icon-background-color'}, 'help');
                $icon-background-color: $colors;
                $icon-color: test-contrast($icon-background-color);

                @if $name == 'warning' { $icon-color: $cl-blue-dark; }

                background-color: $background-color;
                border-right: 3px solid rgba($icon-background-color, 0.3);
                color: $color;

                a {
                    color: $color;
                    &:hover {
	                    @if $name == default {
		                    color: rgba($color, 0.75);
	                    } @else {
	                        color: $icon-background-color;
                        }

                    }
                }

                > .callout-icon {
                    background-color: $icon-background-color;
                    .fa {
                        color: $icon-color;
                    }
                }
            }
        }
    }
}

.callout-sm {
    .callout-icon {
        padding: 10px 0;
        width: 34px;
        .fa {
            font-size: 18px;
            line-height: 16px;
        }
    }
    .callout-body {
        font-size: 14px;
        line-height: 18px;
        padding: 9px 16px;
    }
}
