@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.secondary-nav {
    display: flex;
    height: 58px;

    @media (max-width: map-get($grid-breakpoints, 'md')) {
        display: none;
    }

    .nav {
        margin-left: 89px;

        @media (max-width: map-get($grid-breakpoints, 'lg')) {
            margin-left: 0;
        }

        .nav-item {
            display: flex;
        }
        .nav-link {
            align-items: center;
            display: flex;
            font-family: $font-family-book;
            font-size: 14px;
            line-height: 16px;
            padding: 0 20px;
            position: relative;
            text-decoration: none;
            @media (max-width: map-get($grid-breakpoints, 'lg')) {
                font-size: 14px;
                padding: 0 10px;
            }
            &.active {
                margin-left: -1px;
                margin-right: -1px;
                &:after {
                    border-style: solid;
                    border-width: 11px 11px 0;
                    bottom: -9px;
                    content: ' ';
                    height: 11px;
                    justify-self: center;
                    left: 0;
                    margin: auto;
                    position: absolute;
                    right: 0;
                    width: 11px;
                    z-index: 1000;
                }
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('background-color', 'secondary-navbar');
        border-bottom: themed-component('border', 'secondary-navbar');
        .nav-item {
            border-left: themed-component('nav-item-border', 'secondary-navbar');
            &:last-child {
                border-right: themed-component('nav-item-border', 'secondary-navbar');
            }
        }
        .nav-link {
            color: themed-component('nav-link-color', 'secondary-navbar');
            &:hover {
                color: themed-component('active-nav-background-color', 'secondary-navbar');
            }
            &.disabled {
                color: themed-component('nav-link-disabled-color', 'secondary-navbar');
            }
            &.active {
                background-color: themed-component('active-nav-background-color', 'secondary-navbar');
                color: themed-component('active-nav-color', 'secondary-navbar');
                text-shadow: themed-component('active-nav-text-shadow', 'secondary-navbar');
                &:after {
                    border-color: themed-component('active-nav-after-border', 'secondary-navbar');
                }
            }
        }

    }
    //- Theming ========================================================================================================
}

.title-nav-group {
    .title {
        border-bottom: 0;
        padding: 32px 0 4px;
    }
}