@import '../variables';
@import '../mixins/mixins';
@import '../../node_modules/bootstrap/scss/mixins/_breakpoints.scss';
@import '../themes';

.navbar-search-container {
    left: 0;
    position: absolute;
    top: 80px;
    width: 100%;
    .navbar-search {
        align-items: center;
        flex: 1 1 auto;
        border-radius: 0;
        display: flex;
        flex-direction: row;
        width: 100%;
        white-space: nowrap;
        .form-group {
            margin-bottom: 0;
            margin-left: 89px;
            position: relative;
            width: 100%;
            .clear-search {
                align-items: center;
                display: flex;
                height: 36px;
                justify-content: center;
                position: absolute;
                right: 1px;
                top: 1px;
                width: 36px;
                margin-right: 2px;
                z-index: 4;
            }
        }
        .input-group-btn {
            z-index: 4;
            position: relative;
	        .btn {
		        border-top-left-radius: 0;
		        border-bottom-left-radius: 0;
		        line-height: 12px;
	        }
        }
        .custom-control {
            margin-bottom: 0;
        }
        .form-control {
            border: 0;
            font-family: $font-family-book;
            font-size: 16px;
            font-style: italic;
            line-height: 24px;
            &::placeholder {
                font-size: 16px;
            }
            &:not(.is-invalid) {
                border: 0;
            }
        }
	    .input-group {
		    .form-control {
			    border-top-right-radius: 0;
			    border-bottom-right-radius: 0;
		    }
	    }
        &.not-empty {
            .clear-search {
                display: block;
            }
            .form-control {
                @include navbar-search-notempty;
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('nav-search-background-color', 'navbar');
        box-shadow: themed-component('nav-search-box-shadow', 'navbar');

        .input-icon {
            color: themed-component('nav-search-icon-color', 'navbar');
        }

        .form-control {
            background: themed-component('nav-search-form-background', 'navbar');
            color: themed-component('nav-search-form-color', 'navbar');
            &::placeholder {
                color: themed-component('nav-search-form-placeholder-color', 'navbar');
            }
        }

        &.not-empty {
            .form-control {
                &::placeholder {
                    color: themed-component('nav-not-empty-placeholder-color', 'navbar');
                }
            }

            .input-icon {
                color: themed-component('nav-not-empty-input-icon-color', 'navbar');
            }

            .awesomplete {
                &:after {
                    color: themed-component('nav-not-empty-awesomeplete-color', 'navbar');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.cl-main-nav {
    height: auto;
    padding: 0 15px 0 0;
    z-index: 1020;

    a {
        text-decoration: none;
    }

    .custom-checkbox, .custom-radio {
        .custom-control-input {
            &:checked {
                & ~ .custom-control-indicator {
                    @include set-focus;
                }
            }
        }
    }

    .navbar-brand {
        @include font-size(20px);
        align-items: center;
        display: flex;
        flex: 0 0 auto;
        font-family: $font-family-book;
        line-height: calc-rem(20px);
        margin: 0 65px 0 0;
        padding: 4px 0 0;
    }
    .navbar-logo {
        height: 80px;
        margin-right: 22px;
        margin-top: -4px;
        width: 90px;
    }
    .navbar-collapse {
        .navbar-search {
            display: flex;
            flex-flow: row wrap;
            padding: 0 35px 0 60px;
            position: relative;
            top: 0;
        }
    }
    .navbar-nav, .nav {
        padding-top: 3px;

        .nav-item {
            display: flex;
            //flex: 1;

            &.dropdown {
                .dropdown-menu {
                    &.dropdown-menu-right {
                        left: auto;
                        right: 0;
                    }
                }
            }
        }

        .nav-divider {
            align-self: center;
            display: flex;
            height: 15px;
            margin-left: 20px;
            margin-right: 20px;
            width: 1px;
        }

        .nav-link {
            @include font-size(16px);
            padding: 0;
            white-space: nowrap;
            //font-weight: 700;
	        font-family: $font-family-book;
            letter-spacing: 1px;

	        &.disabled {
		        font-family: $font-family-light;
	        }

            &.active {
                //font-family: $font-family-book;
                position: relative;
	            &:after {
                    content: ' ';
                    position: absolute;
                    bottom: -25px;
		            width: 100%;
                    height: 3px;
		            left: 0;
		            right: 0;
                }
            }

            &.secondary-nav-mobile {
                display: none;
            }

            &.dropdown-toggle {
                &::after {
                    margin-left: 8px;
                    width: 20px;
                    height: 20px;
                    border-width: 0;
                    vertical-align: middle;
                }
	            &.active {
		            &:after {
			            position: relative;
			            bottom: unset;
		            }
	            }
            }
        }
    }

    .nav {
        .nav-item {
            margin-left: 0.5rem;
            margin-right: 0.75rem;
        }
    }

    .navbar-toggler {
        border: 0;
        border-radius: 0;
        padding-right: 15px;
    }

    &.navbar-dark {
        .navbar-close {
            border-bottom: none;
            border-top: none;
            border-right: none;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: 150px;
            transition: box-shadow ease-in-out 0.15s;

            button:focus, &:focus {
                @include set-focus;
            }

            .clear-search {
                font-size: 45px;
                &:hover {
                    cursor: pointer;
                }
            }
        }
    }

    &.navbar-light {
        .navbar-close {
            border-bottom: none;
            border-top: none;
            border-right: none;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: 150px;

            button:focus, &:focus {
                @include set-focus;
            }

            .clear-search {
                font-size: 45px;
                &:hover {
                    cursor: pointer;
                }
            }
        }

        .navbar-nav, .nav {
            .nav-link {
                &.dropdown-toggle {
                    &::after {
                        margin-left: 8px;
                        width: 20px;
                        height: 20px;
                        border-width: 0;
                        vertical-align: middle;
                    }
                }
            }
        }

        .navbar-search-container {
            .navbar-search {
                .form-control {
                    font-family: $font-family-book;
                    &::placeholder {
                        font-size: 16px;
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: transparent;
        background-image: themed-component('nav-background-color', 'navbar');
        border-bottom: themed-component('nav-border-bottom', 'navbar');
        $nav-hide-logo: themed-component('nav-hide-logo', 'navbar');
        $nav-dark-show-color-logo: themed-component('nav-dark-show-color-logo', 'navbar');
        $nav-dark-show-white-logo: themed-component('nav-dark-show-white-logo', 'navbar');

        .navbar-logo {
            @if $nav-dark-show-color-logo == true {
                background: themed-component('clgx-logo-color', 'logo');
            }
            @if $nav-dark-show-white-logo == true {
                background: themed-component('clgx-logo-white', 'logo');
            }
            @if $nav-hide-logo == true {
                background: none;
            }
            background-size: 53px 43px;
            border-right: themed-component('nav-logo', 'navbar');
        }

        .custom-checkbox, .custom-radio {
            .custom-control-input {
                & ~ .custom-control-indicator {
                    border-color: themed-component('nav-checkbox-border-color', 'navbar');
                }
            }
        }

        .navbar-collapse {
            background-color: themed-component('nav-collapse-background-color', 'navbar');
            .navbar-search {
                box-shadow: themed-component('nav-search-collapse-box-shadow', 'navbar');
            }
        }

        .navbar-nav, .nav {
            .nav-divider { background-color: themed-component('nav-dark-divider-background-color', 'navbar'); }

            .nav-link {
                color: themed-component('nav-link-color', 'navbar');

                &:hover { color: themed-component('nav-dark-link-hover-color', 'navbar'); }

                &.disabled {
                    color: themed-component('nav-dark-link-disabled-color', 'navbar');

                    &:hover { color: themed-component('nav-link-disabled-color', 'navbar'); }
                }

                &.dropdown-toggle {
                    &::after { background: themed-component('nav-dark-dropdown', 'navbar'); }
                }

	            &.active {
		            //color: themed-component('nav-dark-link-active-color', 'navbar');
                    &:after {
                        background-color: themed-component('nav-dark-link-active-color', 'navbar');
		            }
	            }
            }
        }

        .navbar-toggler {
            .nav {
                .nav-link {
                    color: themed-component('nav-link-color', 'navbar');
                    &.collapsed {
                        color: themed-component('nav-link-color', 'navbar');
                    }
                    &:hover, &:active, &:focus {
                        color: themed-component('nav-link-color', 'navbar');
                    }
                }
            }
        }

        &.navbar-dark {
            color: themed-component('nav-link-color', 'navbar');
            .custom-checkbox {
                .custom-control-input, input {
                    background-color: themed-component('nav-checkbox-background-color', 'navbar');
                }
            }
            .clear-search {
                color: themed-component('nav-link-color', 'navbar');
                &:hover {
                    color: themed-component('nav-clear-search-color', 'navbar');
                }
            }
            .navbar-close {
                background: themed-component('nav-close-background', 'navbar');
                border-left: themed-component('nav-close-border', 'navbar');
                button:focus, &:focus {
                    background: themed-component('nav-close-button-background-color', 'navbar');
                    border: themed-component('nav-close-button-border-color', 'navbar');
                }
                .clear-search {
                    color: themed-component('nav-close-clear-search-color', 'navbar');
                    &:hover {
                        color: themed-component('nav-close-clear-search-hover-color', 'navbar');
                    }
                }
            }
        }

        &.navbar-light {
            background-image: none;
            background-color: themed-component('nav-light-background-color', 'navbar');
            color: themed-component('nav-light-color', 'navbar');
            $background-color: themed-component('nav-light-background-color', 'navbar');
            $nav-hide-logo: themed-component('nav-hide-logo', 'navbar');
            $nav-light-show-color-logo: themed-component('nav-light-show-color-logo', 'navbar');
            $nav-light-show-white-logo: themed-component('nav-light-show-white-logo', 'navbar');

            .navbar-logo {
                @if $nav-light-show-color-logo == true {
                    background: themed-component('clgx-logo-color', 'logo');
                }
                @if $nav-light-show-white-logo == true {
                    background: themed-component('clgx-logo-white', 'logo');
                }
                @if $nav-hide-logo == true {
                    background: none;
                }
                background-size: 53px 43px;
                border-right: themed-component('nav-light-logo', 'navbar');
            }

            .navbar-brand {
                color: themed-component('nav-light-brand', 'navbar');
            }

            .navbar-toggler {
                .nav {
                    .nav-link {
                        color: themed-component('nav-light-color', 'navbar');
                        &:hover, &:active, &:focus {
                            color: themed-component('nav-light-hover-color', 'navbar');
                        }
                    }
                }
            }

            .navbar-close {
                background: themed-component('nav-close-background', 'navbar');
                border-left: themed-component('nav-light-close-border', 'navbar');

                button:focus, &:focus {
                    background-color: themed-component('nav-light-close-button-background-color', 'navbar');
                    border-color: themed-component('nav-light-close-button-border-color', 'navbar');
                }

                .clear-search { color: themed-component('nav-light-close-clear-search-color', 'navbar'); }
            }

            .navbar-nav, .nav {
                .nav-divider { background-color: themed-component('nav-light-divider-background-color', 'navbar'); }

                .nav-link {
                    color: themed-component('nav-light-link-color', 'navbar');

                    &:hover { color: themed-component('nav-light-link-hover-color', 'navbar'); }


                    &.disabled {
                        color: themed-component('nav-light-link-disabled-color', 'navbar');

                        &:hover { color: themed-component('nav-light-link-disabled-color', 'navbar'); }
                    }

                    &.active, &:active {
                        color: themed-component('nav-light-link-active-color', 'navbar');

                        &:after {
                            background: themed-component('nav-light-link-active-bg-color', 'navbar');
                        }
                    }

                    &.dropdown-toggle {
                        &::after { background: themed-component('nav-light-dropdown-color', 'navbar'); }

                        &.active, &:active {
                            color: themed-component('nav-light-link-active-color', 'navbar');

                            &::after { background: themed-component('nav-light-dropdown-active-color', 'navbar'); }
                        }

                    }
                }
            }

            .navbar-search-container {
                background-color: themed-component('nav-light-search-container-background-color', 'navbar');
                box-shadow: themed-component('nav-light-search-container-box-shadow', 'navbar');

                .navbar-search {
                    box-shadow: themed-component('nav-light-search-box-shadow', 'navbar');

                    .form-control {
                        background: themed-component('nav-light-search-form-background', 'navbar');
                        border: themed-component('nav-light-search-form-border', 'navbar');
                        color: themed-component('nav-light-search-color', 'navbar');

                        &::placeholder {
                            color: themed-component('nav-light-search-form-placeholder-color', 'navbar');
                        }
                    }

                    .input-icon {
                        color: themed-component('nav-light-search-color', 'navbar');
                    }
                }
            }

            .navbar-search {
                .clear-search {
                    color: themed-component('nav-light-clear-search-color', 'navbar');

                    &:hover { color: themed-component('nav-light-close-clear-search-hover-color', 'navbar'); }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

