@font-face {
    font-display: swap;
    font-family: 'clgx-icons';
    font-style: normal;
    font-weight: normal;
    src:  url('../fonts/clgx-icons.eot?d7p78i');
    src:  url('../fonts/clgx-icons.eot?d7p78i#iefix') format('embedded-opentype'),
        url('../fonts/clgx-icons.ttf?d7p78i') format('truetype'),
        url('../fonts/clgx-icons.woff?d7p78i') format('woff'),
        url('../fonts/clgx-icons.svg?d7p78i#icomoon') format('svg');
}

[class^='clgx-icon-'], [class*=' clgx-icon-'] {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-family: 'clgx-icons';
    font-size: inherit;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    line-height: 1;
    position: relative;
    speak: none;
    text-rendering: auto;
    text-transform: none;
    vertical-align: top;
}


