@import '../variables';
@import '../mixins/mixins';
@import '../themes';

@mixin collapse-button {
    align-content: center;
    color: $primary;
    cursor: pointer;
    display: none;
    font-family: FontAwesome;
    font-size: 24px;
    line-height: 24px;
    opacity: 0;
    position: absolute;
    text-align: center;
    top: 4px;
    text-decoration: none;
    transition: opacity 400ms ease-in-out;
    width: 24px;
    &:hover {
        color: darken($primary, 35%);
    }
}

$default-collapse-hover-color: $cl-blue-dark;
@mixin collapse-button-variant($color: $primary, $hover-color: $default-collapse-hover-color) {
    color: $color;
    &:hover {
        color: $hover-color;
    }
}

.layout-drawer {
    display: flex;
    flex-direction: row;
    @media print {
        display: inline;
    }
    height: calc(100vh - 81px);
    overflow: hidden;
    z-index: 1;
    @media screen and (max-width: map-get($grid-breakpoints, 'lg')) {
        height: calc(100vh - 71px);
    }
    @media screen and (max-width: map-get($grid-breakpoints, 'md')) {
        height: calc(100vh - 50px);
        &.with-secondary-nav {
            height: calc(100vh - 51px);
        }
    }

    &.with-secondary-nav {
        height: calc(100vh - 139px);
    }

    .title-command-area {
        background-color: $primary;
        display: flex;
        //margin-top: -8px;
        min-height: 80px;
        padding-left: 32px;
        .heading {
            align-self: center;
            color: $white;
            font-family: $font-family-book;
            font-size: calc-rem(28px);
	        &.small {
		        font-size: calc-rem(24px);
	        }
            @if $showTextShadows == true {
                text-shadow: 0 0 1px rgba($black, 0.9);
            }
        }
        & + .collapse-button {
            // color: $white;
            & + .scroll {
                padding-bottom: 32px;
                padding-top: 24px;
            }
        }

        @media screen and (max-width: map-get($grid-breakpoints, 'lg')) {
            min-height: 80px;
            padding: 0 20px;
            .heading {
                font-size: calc-rem(22px);
            }
        }

        @media screen and (max-width: map-get($grid-breakpoints, 'md')) {
            padding: 0 20px;
            //min-height: 70px;
            .heading {
                font-size: calc-rem(22px);
            }
        }

        @media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
            padding: 0 16px;
            .heading {
                font-size: calc-rem(18px);
            }
        }
    }
    .scroll {
        overflow-y: auto;
        padding-bottom: 32px;
    }

    > .drawer {
        background-color: $seashell;
        //border-top: 8px solid $primary;
        box-shadow: 1px 0 6px 0 rgba($black, 0.45);
        display: flex;
        @media print {
            display: none;
        }
        flex: 0 0 290px;
        flex-direction: column;
        height: 100%;
        position: relative;
        transition: margin-left 500ms ease-in-out;
        width: 290px;
        z-index: 1;

        .collapse-button {
            @include collapse-button;
            right: 8px;
            &:after {
                content: '\f100';
            }
        }

        .drawer-header {
            background-color: rgba($white, 0.8);
            padding: 30px 20px;
        }
        .drawer-body {
            //border-top: 1px solid $alto;
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            overflow-x: hidden;
            overflow-y: auto;
            padding: 30px 20px;
            position: relative;
        }
        .drawer-footer {
            background-color: rgba($white, 0.8);
            //border-top: 1px solid $alto;
            padding: 20px;
            text-align: right;
        }
    }
    .layout-content {
        background-color: $white;
        //border-top: 8px solid $primary;
        overflow-y: auto;
        position: relative;
        width: 100%;
        z-index: 0;
        > .expand-button {
            @include collapse-button;
            left: 8px;
            z-index: 1020;
            &:after {
                content: '\f101';
            }
        }
    }

    //- Theming ========================================================================================================
    @include themify($themes) {
        $collapse-button-color: themed-component('collapse-button-color', 'drawers');
        $collapse-button-hover-color: themed-component('collapse-button-hover-color', 'drawers');
        $layout-content-collapse-button-color: themed-component('layout-content-collapse-button-color', 'drawers');
        $layout-content-collapse-button-hover-color: themed-component('layout-content-collapse-button-hover-color', 'drawers');

        .title-command-area {
            background-color: themed-component('title-command-area-background-color', 'drawers');

            .heading {
                color: themed-component('title-command-area-heading-color', 'drawers');
            }

            & + .collapse-button {
                @include collapse-button-variant(#{$collapse-button-color}, #{$collapse-button-hover-color});
            }
        }

        .collapse-button {
            @include collapse-button-variant($collapse-button-color, $collapse-button-hover-color);
        }

        > .drawer {
            //background-color: $cl-transparent;
            background-image: themed-component('drawer-bg-color', 'drawers');
            border-top: 8px solid themed-component('drawer-border-top-color', 'drawers');
            box-shadow: themed-component('drawer-box-shadow', 'drawers');
            color: themed-component('drawer-color', 'drawers');

            h1, h2, h3, h4, h5, h6,
            .h1, .h2, .h3, .h4, .h5, .h6 {
                color: themed-component('drawer-header-color', 'drawers');
            }

            .collapse-button {
                @include collapse-button-variant($collapse-button-color, $collapse-button-hover-color);
            }

            .drawer-header {
                background-color: themed-component('drawer-header-background-color', 'drawers');
                color: themed-component('drawer-header-color', 'drawers');
            }

            .drawer-body {
                color: themed-component('drawer-body-color', 'drawers');

                .form-group, .input-group {
                    color: themed-component('drawer-body-color', 'drawers');

                    label {
                        color: themed-component('drawer-body-color', 'drawers');
                    }

                    .input-group-addon {
                        color: themed-component('drawer-dark-input-group-icon-color', 'drawers');
                    }
                }

                fieldset {
                    legend {
                        color: themed-component('drawer-body-color', 'drawers');
                    }

                    .custom-control {
                        .custom-control-description {
                            color: themed-component('drawer-color', 'drawers');
                        }
                    }
                }

                .checkbox-group {
                    .checkbox-list {
                        border-color: themed-component('drawer-dark-checkbox-list-border-color', 'drawers');
                    }
                }
            }

            .drawer-footer {
                background-color: themed-component('drawer-footer-background-color', 'drawers');
                color: themed-component('drawer-footer-color', 'drawers');
            }

            &.drawer-light {
                background-image: themed-component('drawer-light-background-color', 'drawers');
                border-top: 8px solid themed-component('drawer-light-border-top-color', 'drawers');

                .drawer-header {
                    background-color: themed-component('drawer-light-header-background-color', 'drawers');
                    color: themed-component('drawer-light-header-color', 'drawers');

                    h2 {
                        color: themed-component('drawer-light-header-color', 'drawers');
                    }
                }

                .drawer-body {
                    color: themed-component('drawer-light-color', 'drawers');

                    fieldset {
                        legend {
                            color: themed-component('drawer-light-color', 'forms');
                        }
                    }

                    .form-group, .input-group {
                        label {
                            color: themed-component('drawer-light-color', 'drawers');
                        }
                    }
                }

                .drawer-footer {
                    background-color: themed-component('drawer-light-footer-background-color', 'drawers');
                    //border-top: 1px solid themed-component('drawer-footer-border-top-color', 'drawers');
                    color: themed-component('drawer-light-footer-color', 'drawers');
                }

                /*+.layout-content {
                   border-top: 8px solid themed-component('drawer-light-layout-content-border-top-color', 'drawers');
               }*/
            }
        }

        .layout-content {
            background-color: themed-component('layout-content-background-color', 'drawers');
            border-top: 8px solid themed-component('layout-content-border-top-color', 'drawers');

            > .expand-button {
                // This sets color for expand without PCA
                @include collapse-button-variant($layout-content-collapse-button-color, $layout-content-collapse-button-hover-color);
            }

            &.drawer-light {
                border-top: 8px solid themed-component('drawer-light-layout-content-border-top-color', 'drawers');
            }
        }

        &.with-page-command {
            > .layout-content {
                .expand-button {
                    color: themed-component('white-color', 'colors');
                }
            }
        }
	    &:not(.with-page-command) {
		    &.collapsed {
			    > .layout-content {
				    .expand-button {
					    color: themed('href-color');
				    }
			    }
		    }
	    }
    }

    .title-command-area {
        $merged: map-merge($theme-colors, $new-brand-colors19);

        @each $name, $color in $merged {
            &.tca-#{$name} {
                $background-color: $color;
                $text-color: test-contrast($background-color);

                @if index('orange' 'orange-dark' 'warning' 'amethyst',$name) { $text-color: $cl-blue-dark; }

                color: $text-color;
                background-color: $background-color;

                .heading { color: $text-color; }
            }
        }

        &.tca-default { background-color: $cl-gray-dark; }

        /*background-color: themed-component('title-command-area-background-color', 'drawers');
        .heading {
            color: themed-component('title-command-area-heading-color', 'drawers');
        }
        & + .collapse-button {
            @include collapse-button-variant($collapse-button-color, $collapse-button-hover-color);
        }*/
    }

    &.slider {
        > .drawer {
            > .collapse-button {
                display: block;
                opacity: 1;
            }
        }
        > .layout-content {
            > .expand-button {
                display: none;
                opacity: 0;
            }
        }
    }

    &.collapsed {
        > .drawer {
            margin-left: -290px;
            > .collapse-button {
                opacity: 0;
            }
        }
        > .layout-content {
            > .expand-button {
                display: block;
                opacity: 1;
            }
        }
    }

    &.with-page-command {
        > .drawer {
            border-top: 0;
            > .collapse-button {
                color: $white;
            }
        }
        > .layout-content {
            border-top: 0;
            > .expand-button {
                //color: yellow;
            }
        }
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'lg')) {
    .layout-drawer {
        height: 100%;
        flex: 1 1 auto;
        > .drawer {
            height: auto;
        }
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'md')) {
    .layout-drawer {
        height: 100%;
        flex: 1 1 auto;
        > .drawer {
            height: auto;
        }
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
    .layout-drawer {
        height: 100%;
        flex: 1 1 auto;
        > .drawer {
            height: auto;
        }
    }
}
