@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.custom-autocomplete {

    .input-group {
        .form-control {
            // border-radius: 0;
        }
    }

    .clear-search {
        display: block;
        line-height: 36px;
        padding: 0 8px;
        position: absolute;
        right: 0;
        z-index: 8888;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .clear-search {
            color: themed-component('clear-search-color', 'autocomplete');
            &:hover {
                color: themed-component('clear-search-hover-color', 'autocomplete');
            }
        }
    }
    //- Theming ========================================================================================================
}

.autocomplete-container {
    border-radius: 4px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    position: absolute;
    top: 42px;
    width: 100%;
    .table {
        margin-bottom: 5px;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('autocomplete-background-color', 'autocomplete');
        border: themed-component('autocomplete-border', 'autocomplete');
        box-shadow: themed-component('autocomplete-box-shadow', 'autocomplete');
    }
    //- Theming ========================================================================================================
}
.autocomplete-container-alert {
    border-radius: 4px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    position: absolute;
    top: 42px;
    width: 100%;
    //- Theming ========================================================================================================
    @include themify($themes) {
        box-shadow: themed-component('autocomplete-box-shadow', 'autocomplete');
    }
}
