@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.table {
    border-radius: 0;
    > tbody  {
        > tr {
            &.secondary-heading {
                > td {
                    font-family: $font-family-book;
                    font-size: calc-rem(16px);
                    font-weight: 400;
                    height: 50px;
                    line-height: calc-rem(18px);
                    padding: 8px 16px 6px;
                    text-align: left !important;
                    vertical-align: middle;
                }
            }
            &.child-row {
                td {
                    border-top: 0;
                    height: 0;
                    padding: 0;
                }
            }
        }
    }
    > tfoot {
        > tr {
            height: auto;
            > td {
                margin: 0;
                padding: 0;
            }
        }
    }
    &.table-bordered {
        thead {
            th {
                border-bottom-width: 1px;
            }
        }
    }
    &.table-sm {
        thead, tbody {
            td, th {
                font-size: calc-rem(14px);
                height: auto;
                line-height: calc-rem(14px);
                padding: calc-rem(10px);
                .btn-xs {
                    height: 20px;
                }
            }
        }
        tfoot {
            .cl-pagination {
                height: 40px;
            }
        }
    }
    &.col-equal {
        table-layout: fixed;
    }
    &.col-no-dividers {
        @include sel-table-cells {
            border-left: 0;
            border-right: 0;
        }
    }
    &.table-search-highlights {
        tr {
            cursor: pointer;
        }
        em {
            font-style: normal;
        }
        a {
            text-decoration: none;
            font-family: $font-family-book;
        }
    }

    &.table-no-row-lines {
        td {
            border-top: 0;
        }
    }

    &.table-solid {
        @include sel-thead-th {
            border-bottom: 0;
            border-top: 0;
        }
        @include sel-tbody-td {
            code {
                font-size: calc-rem(12px);
                // line-height: calc-rem(12px);
                margin: 0;
            }
        }
    }

    //col-[index]-align-right
    @for $i from 1 through $intColumnMax {
        &.col-#{$i}-align-right {
            @include sel-thead-th {
                &:nth-child(#{$i}) {
                    text-align: right;
                }
            }
            @include sel-tbody-td {
                &:nth-child(#{$i}) {
                    text-align: right;
                }
            }
        }
    }
    //col-[index]-align-center
    @for $i from 1 through $intColumnMax {
        &.col-#{$i}-align-center {
            @include sel-thead-th {
                &:nth-child(#{$i}) {
                    text-align: center;
                }
            }
            @include sel-tbody-td {
                &:nth-child(#{$i}) {
                    text-align: center;
                }
            }
        }
    }

    //col-[index]-slim
    @for $i from 1 through $intColumnMax {
        &.col-#{$i}-slim {
            @include sel-thead-th {
                &:nth-child(#{$i}) {
                    width: 1%;
                }
            }

            @include sel-tbody-td {
                &:nth-child(#{$i}) {
                    width: 1%;
                }
            }
        }
    }

    //col-[index]-bloat
    @for $i from 1 through $intColumnMax {
        &.col-#{$i}-bloat {
            @include sel-thead-th {
                white-space: nowrap;
                width: 1%;

                &:nth-child(#{$i}) {
                    width: 100%;
                }
            }
            @include sel-tbody-td {
                white-space: nowrap;
                width: 1%;

                &:nth-child(#{$i}) {
                    width: 100%;
                }
            }
        }
    }

    //col-[index]-btns
    @for $i from 1 through $intColumnMax {
        &.col-#{$i}-buttons, &.col-#{$i}-icons {
            @include sel-thead-th {
                &:nth-child(#{$i}) {
                    width: 1%;
                }
            }
            @include sel-tbody-td {
                &:nth-child(#{$i}) {
                    padding-left: 6px;
                    padding-right: 6px;
                    width: 1%;
                }
            }
            > tbody {
                > tr {
                    &.secondary-heading {
                        > td {
                            padding-left: 16px;
                            padding-right: 16px;
                            width: auto;
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: map-get($grid-breakpoints, 'md')) {
        @include sel-table-cells {
            font-size: calc-rem(14px);
            height: auto;
            line-height: calc-rem(14px);
            padding: calc-rem(10px);
        }
        tfoot {
            .cl-pagination {
                height: 40px;
            }
        }
    }

    @include sel-table-cells {
        label {
            margin-bottom: 0;
        }
        .custom-control.custom-checkbox {
            margin-right: 0;
            min-height: 0;
            padding-left: 0;
            .custom-control-indicator {
                &:after {
                    left: 2px;
                    top: 0;
                }
            }
        }
        .cl-keyvalue {
            &.inline {
                .cl-key {
                    padding-right: 2px;
                }
            }
        }
    }

    @include sel-thead-th {
        font-family: $font-family-book;
        font-size: calc-rem(16px);
        font-weight: 400;
        height: 50px;
        line-height: calc-rem(18px);
        padding: 8px 16px 6px;
        vertical-align: middle;
        .sortable {
            cursor: pointer;
            margin-right: -8px;
        }
    }

    @include sel-tbody-td {
        font-size: calc-rem(16px);
        height: 46px;
        line-height: calc-rem(18px);
        padding: calc-rem(8px) calc-rem(16px) calc-rem(6px);
        vertical-align: middle;
        .collapse-button {
            padding-left: 11px;
            position: relative;
            &:before {
                font-family: FontAwesome;
                font-size: calc-rem(18px);
                left: 0;
                line-height: 10px;
                margin-top: -4px;
                position: absolute;
                top: 50%;
                transition: transform 300ms ease-in-out;
                width: 10px;
            }
            &.collapsed {
                &:before {
                    transform: rotate(-90deg);
                    transition: transform 300ms ease-in-out;
                }
            }
        }
        > .collapse, > .collapsing {
            .collapse-content {
                padding: calc-rem(16px) calc-rem(60px);
            }
        }
    }

    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('background-color', 'tables');
        > tbody {
            > tr {
                &.secondary-heading {
                    > td {
                        background-color: themed-component('secondary-heading-background-color', 'tables');
                        color: themed-component('secondary-heading-color', 'tables');
                    }
                }
            }
        }
        &.table-bordered {
            border-color: themed-component('border-color', 'tables');
            th, td {
                border-color: themed-component('border-color', 'tables');
            }
        }
        &.table-search-highlights {
            color: themed-component('search-highlights-color', 'tables');
            tr {
                &:hover {
                    background-color: themed-component('search-highlights-row-hover-background-color', 'tables');
                }
            }
            .highlight-row {
                > td {
                    background-color: themed-component('search-highlights-row-hover-background-color', 'tables');
                }
            }
            em {
                background-color: themed-component('search-highlights-em-background-color', 'tables');
                color: themed-component('search-highlights-em-color', 'tables');
            }
            a {
                color: themed-component('search-highlights-link-color', 'tables');
            }
        }
        &.table-striped {
            a {
                color: themed-component('striped-link-color', 'tables');
                &:hover {
                    color: themed-component('striped-link-hover-color', 'tables');
                }
            }
            tbody {
                tr {
                    &:nth-of-type(odd) {
                        background-color: themed-component('striped-odd-row-background-color', 'tables');
                    }
                }
            }

            &:not(.table-accordion) {
                > tbody {
                    tr {
                        &.secondary-heading ~ tr:nth-child(odd) {
                            background-color: themed-component('striped-not-accrdn-sec-heading-background-color', 'tables');
                        }

                        &:first-child:not(.secondary-heading) ~ tr:nth-child(even) {
                            background-color: themed-component('striped-not-accrdn-sec-heading-background-color', 'tables');
                        }
                    }
                }
            }
        }
        &.table-accordion {
            &.table-striped {
                > tbody {
                    // Every other pair of tr tags (e.g., y n, n n, y n, n n, y n)
                    tr:nth-child(4n + 3), tr:first-child.secondary-heading ~ tr:nth-child(4n + 4) {
                        background-color: themed-component('striped-not-accrdn-sec-heading-background-color', 'tables');
                    }
                }
            }
        }
        &.table-solid {
            @include sel-thead-th {
                background-color: themed-component('solid-thead-background-color', 'tables');
                color: themed-component('solid-thead-color', 'tables');
            }
            @include sel-tbody-td {
                border-top: themed-component('solid-tbody-border-top', 'tables');
                color: themed-component('solid-tbody-color', 'tables');
            }
            background-color: themed-component('solid-background-color', 'tables');
        }

        @include sel-thead-th {
            background-color: themed-component('thead-background-color', 'tables');
            color: themed-component('thead-color', 'tables');
            .sortable {
                .fa {
                    &:after {
                        content: themed-component('thead-sort-default-icon', 'tables');
                    }
                }
                &.asc {
                    .fa {
                        &:after {
                            content: themed-component('thead-sort-asc-icon', 'tables');
                        }
                    }
                }
                &.desc {
                    .fa {
                        &:after {
                            content: themed-component('thead-sort-desc-icon', 'tables');
                        }
                    }
                }
            }
        }

        @include sel-tbody-td {
            .collapse-button {
                color: themed-component('collapse-arrow-cell-color', 'tables');
                &:before {
                    color: themed-component('collapse-arrow-color', 'tables');
                    content: themed-component('collapse-icon', 'tables');
                }
            }
            > .collapse, > .collapsing {
                .collapse-content {
                    background-color: themed-component('collapse-content-background-color', 'tables');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

@each $name, $colors  in $all-colors {
    @include themify($themes, true) {
        $color: themed-component(#{$name + '-color'}, 'colors');
        $background-color: themed-component(#{$name + '-background-color'}, 'colors');

        .table-#{$name} {
            &, > th, > td {
                background-color: $background-color;
            }
        }
        .table-hover {
            $hover-background: darken($background-color, 5%);

            .table-#{$name} {
                &:hover {
                    background-color: $hover-background;

                    > td, > th {
                        background-color: $hover-background;
                    }
                }
            }
        }
    }
}

.table-responsive {
    margin-bottom: 1rem;
    .table {
        margin-bottom: 0;
    }
}