@import '../variables';
@import '../mixins/mixins';
@import '../themes';
@import '../functions';

$btn-shadow: 0 0 4px 1px rgba($black, 0.25);

.btn {
    @include themify($themes) {
        box-shadow: themed-component('box-shadow', 'buttons');
    }
    align-items: center;
    display: inline-flex;
    font-family: $font-family-book;
    font-size: 16px;
    letter-spacing: -0.03em;
    line-height: calc-rem(16px);
    margin: 0;
    padding: calc-rem(12px) calc-rem(26px);

    & + .btn {
        margin-left: 6px;
    }

    & + .btn-block {
        margin-left: 0;
    }

    &:not(:disabled) {
        cursor: pointer;
    }

    &.btn-block {
        justify-content: center;
    }

    &:not(.btn-icon-only) {
        .fa {
            font-size: calc-rem(16px);
            line-height: calc-rem(16px);
        }

        &.btn-xs {
            font-size: 11px;
            line-height: calc-rem(11px);
            padding: calc-rem(5px) calc-rem(10px);


            .fa {
                font-size: 9px;
            }
        }

        &.btn-sm {
            font-size: 14px;
            line-height: calc-rem(14px);
            padding: calc-rem(6px) calc-rem(14px);


            .fa {
                font-size: 12px;
            }
        }

        &.btn-lg {
            font-size: calc-rem(19px);
            line-height: calc-rem(19px);
            padding: calc-rem(17px) calc-rem(36px);

            .fa {
                font-size: calc-rem(18px);
            }
        }
    }

    &.btn-icon-only {
	    padding: calc-rem(12px) calc-rem(13px);

        .fa {
            font-size: 18px;
            text-align: center;
            width: 16px;
        }

        &.btn-lg {
            padding: calc-rem(14px) calc-rem(18px);

            .fa {
                font-size: 22px;
                text-align: center;
                width: 18px;
            }
        }

        &.btn-sm {
            padding: calc-rem(6px) calc-rem(9px);


            .fa {
                font-size: 14px;
                text-align: center;
                width: 12px;
            }
        }

        &.btn-xs {
	        padding: calc-rem(4px) calc-rem(4px);

            .fa {
                font-size: 11px;
                text-align: center;
                width: 11px;
            }
        }
    }

    &.dropdown-toggle {
        &.dropdown-toggle-split {
            padding-left: 8px;
            padding-right: 8px;

            &:after {
                margin-left: 0;
            }
        }
    }
}

a {
    .btn {
        &.disabled {
            box-shadow: none;
        }
    }
}

.btn-group {
	border-radius: 0.25rem;
    @include themify($themes) {
         // border-radius: themed-component('btn-border-radius-default', 'buttons');

        > .btn {
            box-shadow: none;

            & + .btn {
                margin-left: 0;
            }
        }

        @each $name, $color in $all-colors {
            box-shadow: themed-component('box-shadow', 'buttons');

            > .btn {
                & + .btn-#{$name} {
                    border-left: themed-component(#{$name + '-split-button-divider-color'}, 'dropdowns');
                }
            }
        }
    }
}

// for bootstraps btn toggle replacement
.btn-toggle-radio {
    input {
        &[type='radio'] {
            clip: rect(0, 0, 0, 0);
            pointer-events: none;
            position: absolute;
        }
    }
}


    // Theming ========================================================================================================
$merged: map-merge($message-colors, $theme-colors);
$merged: map-merge($merged, $buttonColors);
$merged: map-merge($merged, $new-brand-colors19);
@each $name, $color in $merged {
    .btn-#{$name} {
        @include themify($themes) {
            $background-color: $color;
            $background-img: linear-gradient(to right, $color, darken($color, 6%));
            $border-color: $background-color;
            $text-color: test-contrast($background-color);

            $hover-background-color: darken($background-color, 6%);
            $hover-border-color: $cl-transparent;
            $hover-color: test-contrast($hover-background-color);

            @if ($name == 'white') { display: none; }
            @if index('default' 'secondary' 'light', $name) { $text-color: $black; }
            @if index('orange' 'orange-dark' 'warning' 'amethyst',$name) { $text-color: $cl-blue-dark; $hover-color: $cl-blue-dark; };

            background-color: $background-color;
            border-color: $border-color;
            color: $text-color;
            background-size: 205% 100%;
            background-image: $background-img;
            transition-property: background-position, color, border-color;
            transition-duration: 0.2s, 0.2s, 0.3s;

            &:hover {
                background-color: $background-color;
                border-color: $hover-background-color;
                color: $hover-color;
                background-position: -97% 0;
            }

            &:focus,
            &.focus {
                @include set-focus;
                background-color: $background-color;
                border-color: $hover-background-color;
                color: $hover-color;
                background-position: -97% 0;
            }

            &.disabled,
            &:disabled {
                background-color: $background-color;
                border-color: $background-color;
	            color: $text-color;
                opacity: themed-component('disabled-opacity', 'buttons');

                &:hover {
                    background-color: $background-color;
                    border-color: $background-color;
                    opacity: themed-component('disabled-opacity', 'buttons');
                }
            }

            &:not([disabled]):not(.disabled):active,
            &:not([disabled]):not(.disabled).active,
            .show > &.dropdown-toggle {
                @include set-focus;
                background-color: $background-color;
                border-color: $hover-background-color;
                color: $hover-color;
                background-position: -97% 0;

	            &::after {
		            margin-left: 8px;
	            }

	            &.dropdown-toggle-split {
		            &::after {
			            margin-left: 0;
		            }
	            }
            }

            &:not([disabled]):not(.disabled):active:focus {
                @include set-focus;
            }

            &.dropdown-toggle {
                @include dropdown-arrow-variant($text-color);
                &.dropdown-toggle-split {
	                &::after {
		                margin-left: 0;
	                }
                }
            }
        }
    }

    .btn-outline-#{$name} {
        @include themify($themes) {
            $background-color: $cl-white;
            $background-img: linear-gradient(to left, $color 50%, $cl-white 50%);
            $border-color: $color;
            $text-color: $border-color;
            $hover-background-color: $text-color;
            $hover-border-color: $border-color;
            $hover-text-color: test-contrast($hover-background-color);

            @if ($name == 'white') {
                $background-color: $cl-transparent;
                $background-img:  linear-gradient(to right, $cl-transparent 50%, $cl-white 50%);
                //$hover-text-color: $cl-blue-dark;
            }
            @if index('default' 'secondary' 'light', $name) { $text-color: $cl-black; }
            @if index('red' 'blue' 'green' 'orange', $name) {
                $border-color: map-get($new-brand-colors19, $name + '-dark');
                $text-color: $border-color;
            }
            @if index('orange' 'orange-dark' 'warning' 'amethyst',$name) { $hover-text-color: $cl-blue-dark; }

	        @if $name == 'primary' { $text-color: $cl-blue-adj; }

            background-color: $background-color;
            border-color: $border-color;
            color: $text-color;
            background-size: 205% 100%;
            background-image: $background-img;
            transition-property: background-position, color, border-color;
            transition-duration: 0.2s, 0.2s, 0.3s;

            &:hover {
                //@if index('default' 'secondary' 'light', $name) { $hover-text-color: $cl-black; }
	            //@if index('red' 'blue' 'green' 'orange' 'orange-dark' 'link', $name) {
		        //    $hover-text-color: $black;
	            //}
                @if ($name == 'white') { $hover-background-color: $cl-transparent; }

                background-color: $hover-background-color;
                border-color: $hover-border-color;
                color: $hover-text-color;
                background-position: -97% 0;
            }

            &:focus,
            &.focus {
                @include set-focus;
            }

            &.disabled,
            &:disabled {
	            box-shadow: none;
                background-color: $background-color;
                border-color: $border-color;
                color: $text-color;
                opacity: themed-component('disabled-opacity', 'buttons');

                &:hover {
                    background-color: transparent;
                    opacity: themed-component('disabled-opacity', 'buttons');
                }
            }

            &:not([disabled]):not(.disabled):active,
            &:not([disabled]):not(.disabled).active,
            .show > &.dropdown-toggle {
                @include set-focus;
                background-color: $hover-background-color;
                border-color: $hover-border-color;
                color: $hover-text-color;
                background-position: -97% 0;

	            @include dropdown-arrow-variant($hover-text-color);

	            &::after {
		            margin-left: 8px;
	            }
            }

            &:not([disabled]):not(.disabled):active:focus {
                @include set-focus;

	            &.dropdown-toggle-split {
		            &::after {
			            margin-left: 0;
		            }
	            }
            }

            &.dropdown-toggle {
                &:hover {
	                //@if index('default' 'primary' 'secondary' 'red' 'blue' 'green' 'orange' 'orange-dark' 'light' 'link', $name) {
		                @include dropdown-arrow-variant($hover-text-color);
	                //} @else {
		            //    @include dropdown-arrow-variant($cl-white);
	                //}
                }

	            @include dropdown-arrow-variant($text-color);
                &:focus {
                    @include set-focus;
                }
                &.dropdown-toggle-split {
                    &::after { margin-left: 0 ;}
                }
            }
        }
    }

    .show {
        > .btn-#{$name}, > .btn-outline-#{$name} {
            &.dropdown-toggle {
                &:focus {
                    @include set-focus;
                }
            }
        }
    }
}

.dropdown-toggle {
    @include dropdown-arrow-variant;
}

.dropdown {
    & + .dropdown {
        margin-left: 6px;
    }
    // the vertical divider line between the split button dropdown
    @each $name, $color in $all-colors {
        .btn + .btn-#{$name} {
            @include themify($themes) {
                border-left: themed-component(#{$name + '-split-button-divider-color'}, 'dropdowns');
            }
        }
    }
    .btn-block {
        justify-content: flex-start;
        padding-right: 30px;
        &::after {
            position: absolute;
            right: 6px;
        }
    }

    .btn {
        //padding: calc-rem(12px) calc-rem(26px) calc-rem(10px);
        &:first-child {
            padding: calc-rem(12px) calc-rem(24px) calc-rem(12px) calc-rem(26px);
        }
        &.btn-xs:first-child {
            padding: calc-rem(5px) calc-rem(8px) calc-rem(5px) calc-rem(10px);
        }
        &.btn-sm:first-child {
            padding: calc-rem(6px) calc-rem(12px) calc-rem(6px) calc-rem(14px);
        }
        &.btn-lg:first-child {
            padding: calc-rem(17px) calc-rem(33px) calc-rem(17px) calc-rem(35px);
        }
        &.btn-group {
            .btn {
                &:first-child {
                    padding: calc-rem(12px) calc-rem(26px);
                }
                &:not(.btn-icon-only) {
                    &.btn-xs:first-child  {
                        padding: calc-rem(5px) calc-rem(10px);
                    }
                    &.btn-sm:first-child  {
                        padding: calc-rem(6px) calc-rem(14px);
                    }
                    &.btn-lg:first-child  {
                        padding: calc-rem(17px) calc-rem(35px);
                    }
                }
            }
        }
    }
}

.dropdown-menu {
    @include themify($themes) {
        background-color: themed-component('menu-background-color', 'dropdowns');
        border: themed-component('menu-border', 'dropdowns');
        box-shadow: themed-component('menu-box-shadow', 'dropdowns');
    }
    background-clip: border-box;
    border-radius: 0;
    margin-top: 2px;
}

.dropdown-toggle-split {
    //padding-left: 8px;
    //padding-right: 8px;
    &:after {
        margin-left: 0;
    }
}

.dropdown-item {
    @include themify($themes) {
        color: themed-component('item-color', 'dropdowns');
        &.active, &:active, &:hover {
            background-color: themed-component('item-background-hover-color', 'dropdowns');
            color: inherit;
        }
    }
}

.dropdown-header {
    @include themify($themes) {
        color: themed-component('item-header-color', 'dropdowns');
    }
    font-family: $font-family-book;
}

.dropdown-divider {
    @include themify($themes) {
        border-color: themed-component('item-divider-color', 'dropdowns');
    }
}

.input-group {
    position: relative;

    .btn {
        box-shadow: 0 0 4px 0 rgba($black, 0.25);
        margin: 0;
        padding-bottom: 0;
        padding-top: 1px;

        &:focus {
            @include set-focus;
        }
    }

    .input-group-append {
        z-index: 2;

        .btn {

        }
    }

    .input-group-prepend {
        .btn {

        }
    }

    &.input-group-sm {
        .btn {
            padding: 1px 14px 0;
        }
    }

    &.input-group-lg {
        .btn {
            padding: 0 36px;
        }
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
    .btn {
        justify-content: center;
        width: 100%;
    }
}
