@import '../variables';
@import '../mixins/mixins';
@import '../../node_modules/select2/src/scss/theme/default/layout';
@import '../../node_modules/select2/src/scss/theme/classic/layout';
@import '../themes';

.select2-container {
    box-sizing: border-box;
    display: inline-block;
    line-height: 23px;
    margin: 0;
    min-width: 100px;
    position: relative;
    vertical-align: middle;

    @import '../../node_modules/select2/src/scss/single';
    @import '../../node_modules/select2/src/scss/multiple';

    &:disabled {
        &::placeholder {
            font-size: 15px;
        }
    }

    [type='search'] {
        &::placeholder {
            font-size: 15px;
        }
    }

    .select2-selection--single, .select2-selection--multiple {
        min-height: 38px;
        padding-left: 7px;
        padding-right: 21px;

        .select2-selection__rendered {
            padding-right: 12px;
        }
    }

    &.select2-container--disabled {
        cursor: not-allowed;
    }

    &:focus {
        outline: none;
    }

    .select2-results {
        > .select2-results__options {
            max-height: inherit;
            overflow-y: visible;

            &[aria-multiselectable='true'] {
                max-height: 240px;
                overflow-y: scroll;
            }
        }
    }

    //- Theming ========================================================================================================
    @include themify($themes) {
        &:disabled {
            background-color: themed-component('select-dropdown-disabled-background-color', 'select');

            &::placeholder {
                color: themed-component('select-color', 'select');
            }
        }
        [type='search'] {
            &::placeholder {
                color: themed-component('select-color', 'select');
            }
        }
        .select2-results__option--highlighted {
            &[aria-selected] {
                background-color: themed-component('select-dropdown-selected-background-color', 'select');
                color: themed-component('select-dropdown-selected-color', 'select');
            }
        }

        .select2-results__option {
            &[aria-selected='true'] {
                background-color: themed-component('select-dropdown-selected-background-color', 'select');
                color: themed-component('select-dropdown-selected-color', 'select');
            }
        }
    }
    //- Theming ========================================================================================================
}

.select2-container--default {
    .select2-selection--single, .select2-selection--multiple {
        border-radius: 0;

        .select2-selection__arrow {
            display: none;
        }

        .select2-selection__rendered, .select2-container {
            font-size: $font-size-base;
            font-weight: $font-normal;
        }
    }

    .select2-selection--multiple, .select2-selection--single {
        position: relative;
    }
    &.select2-container--open {
        .select2-dropdown--above, .select2-dropdown--below {
            border-radius: 0;
        }
        .select2-dropdown--above {
            margin-top: -2px;
        }
        .select2-dropdown--below {
            margin-top: 2px;
        }
        .select2-selection--single, .select2-selection--multiple {
            &:before {
                content: '';
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .select2-selection--single, .select2-selection--multiple {
            border: themed-component('select-border', 'select');
            .select2-selection__rendered, .select2-container {
                color: themed-component('select-color', 'select');
            }
        }
        .select2-selection--multiple, .select2-selection--single {
            background: themed-component('select-background', 'select');
            background-size: 20px 20px;
        }
        &.select2-container--open {
            .select2-dropdown--above, .select2-dropdown--below {
                border: themed-component('select-border', 'select');
                box-shadow: themed-component('select2-box-shadow', 'select');
            }
        }

    }
    //- Theming ========================================================================================================
}

.select2-results__option {
    font-size: 15px !important;
    padding: 2px 14px !important;
}

.select2-container--default .select2-results__group {
    padding: 4px 0;
}

.select2-container--focus:not(.select2-container--disabled) {
    @include set-focus;
    border-radius: 0;
    margin-bottom: 0;
    outline-width: 0;
}

.select2-dropdown {
    border-radius: 0;
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('select2-dropdown-background-color', 'select');
    }
    //- Theming ========================================================================================================
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
    border: 0;
    border-radius: 0;
    display: flex;
    font-family: $font-family-light;
    font-size: calc-rem(16px);
    padding: 0 8px 0 0;

    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('select-dropdown-selected-background-color', 'select');
        color: themed-component('select-dropdown-selected-color', 'select');
    }
    //- Theming ========================================================================================================
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    float: none;
    font-family: $font-family-light;
    font-weight: normal;
    padding: 0 8px;
    margin-right: 0;
    //- Theming ========================================================================================================
    @include themify($themes) {
        color: themed-component('select-dropdown-selected-color', 'select');
        &:hover {
            color: themed-component('select-dropdown-selected-hover-color', 'select');
        }
    }
    //- Theming ========================================================================================================
}

.select2-container--default.select2-container--focus:not(.select2-container--disabled) .select2-selection--multiple {
    //- Theming ========================================================================================================
    @include themify($themes) {
        border: themed-component('select-border', 'select');
    }
    //- Theming ========================================================================================================
}

.select2-container--disabled {
    &.select2-container--focus .select2-selection--multiple {
        cursor: not-allowed;

        input {
            cursor: not-allowed;

            &:disabled {
                opacity: 0.65;
            }
        }
    }

    .selection {
        .select2-selection {
            opacity: 0.65;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        &.select2-container--focus .select2-selection--multiple {
            border: themed-component('select-border', 'select');
        }
    }
    //- Theming ========================================================================================================
}

.select2-search__field::placeholder {
    font-size: 1rem !important;
    font-weight: $font-normal;
    //- Theming ========================================================================================================
    @include themify($themes) {
        color: themed-component('select-color', 'select') !important;
    }
    //- Theming ========================================================================================================
}

.select2-container--default .select2-search--dropdown .select2-search__field {
    border: 0;
    &.placeholder {
        font-style: italic;
    }
}

.select2-container .select2-search--inline .select2-search__field {
    margin-left: -3px !important;
    margin-top: 7px !important;
}

.cl-multi-select.custom-select.is-invalid.select2-hidden-accessible {
    + span {
        border-radius: 0;
        transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;

        & .selection {
            outline-width: 0;

            .select2-selection {
                border: 0;

                &.select2-selection--single, &.select2-selection--multiple {
                    min-height: 36px;
                }
            }
        }

        &.select2-container--focus:not(.select2-container--disabled) {
            transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
            @include set-focus;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        + span {
            border: themed-component('select-invalid-dropdown-border', 'select');
            &.select2-container--focus:not(.select2-container--disabled) {
                color: themed-component('select-invalid-dropdown-color', 'select');
            }
        }
    }
    //- Theming ========================================================================================================
}

@import '../../node_modules/select2/src/scss/dropdown';

.select2-close-mask {
    border: 0;
    display: block;
    filter: alpha(opacity=0);
    height: auto;
    left: 0;
    margin: 0;
    min-height: 100%;
    min-width: 100%;
    opacity: 0;
    padding: 0;
    position: fixed;
    top: 0;
    width: auto;
    z-index: 99;
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('select2-close-mask', 'select');
    }
    //- Theming ========================================================================================================
}

.select2-hidden-accessible {
    border: 0 !important;
    clip: rect(0 0 0 0) !important;
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important;
}
