@import '../variables';

@mixin range-track() {
    //border: 1px solid $background;
    border-radius: 20px;
    cursor: pointer;
    height: 20px;
    width: 100%;
}
@mixin range-thumb() {
    -webkit-appearance: none;
    border: 0;
    border-radius: 24px;
    cursor: pointer;
    height: 24px;
    margin-top: -2px;
    transition: scale 0.1s;
    width: 24px;
    &:active {
        transform: scale(1.075);
    }
}
@mixin range-lower() {
    -webkit-appearance: none;
}