@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.tertiary-nav {
    .nav {
        .nav-link {
            font-family: $font-family-book;
            padding: 0.75rem calc-rem(20px) 0.625rem;
            position: relative;
            text-decoration: none;
            &.active {
                &:after {
                    background-color: $primary;
                    bottom: 0;
                    content: ' ';
                    height: 3px;
                    left: 0;
                    position: absolute;
                    right: -1px;
                }
            }
        }
        &.nav-pills {
            padding-bottom: 7px;

            .nav-item {
                margin-right: 4px;

                .nav-link {
                    border-radius: 8px;
                    font-family: $font-family-book;
                    padding: 0.75rem calc-rem(26px) 0.625rem;

                    &.active {
                        &:after { display: none; }
                    }
                }
            }
        }
        &.nav-tabs {
            border-bottom: 0;
            .nav-item {
                margin-bottom: -1px;
                & + .nav-item {
                    margin-left: -1px;
                }
            }
            .nav-link {
                border-bottom: 0;
                border-radius: 0;
                font-family: $font-family-book;
                padding: 0.75rem calc-rem(26px) 0.625rem;
                position: relative;
                &.active {
                    &:after {
                        display: none;
                    }
                }
                &.disabled {
                    cursor: not-allowed;
                }
            }
        }
    }
    & + .card {
        border-width: 1px;
        border-style: solid;
        border-top: 0;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        border-bottom: themed-component('border', 'tertiary-nav');
        .nav {
            .nav-link {
                color: themed-component('nav-link-color', 'tertiary-nav');
                &.active {
                    color: themed-component('nav-link-active-color', 'tertiary-nav');
                    &:after {
                        background-color: themed-component('nav-link-active-after-background-color', 'tertiary-nav');
                    }
                }
            }
            &.nav-pills {
                .nav-item {
                    .nav-link {
                        color: themed-component('nav-pill-link-color', 'tertiary-nav');
                        &.active {
                            color: themed-component('nav-pill-active-color', 'tertiary-nav');
                        }
                    }
                }
            }
            &.nav-tabs {
                .nav-link {
                    border: themed-component('border', 'tertiary-nav');
                    &.active {
                        border-color: themed-component('nav-tab-active-border', 'tertiary-nav');
	                    color: themed-component('nav-tab-active-color', 'tertiary-nav');
                    }
                    &:hover {
                        &:not(.active) {
                            background-color: themed-component('nav-tab-hover-background-color', 'tertiary-nav');
                            border-color: themed-component('card-border', 'tertiary-nav');
                        }
                    }
                    &.disabled {
                        background-color: themed-component('nav-tab-disabled-background-color', 'tertiary-nav');
                        opacity: themed-component('nav-tab-disabled-opacity', 'tertiary-nav');
                    }
                }
            }
        }
        & + .card {
            border-color: themed-component('teriary-nav-card-border', 'tertiary-nav');

            .card-body { border: none; }
        }
    }
    //- Theming ========================================================================================================
}
