@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.alert {
    border: 0;
    border-radius: 0;
    line-height: calc-rem(20px);
    padding: calc-rem(20px) calc-rem(24px) calc-rem(20px) calc-rem(56px);

    .alert-heading {
        line-height: inherit;
        margin-bottom: calc-rem(8px);
    }

    &:before {
        content: ' ';
        font-family: FontAwesome;
        font-size: calc-rem(22px);
        left: 20px;
        line-height: calc-rem(18px);
        margin-top: 2px;
        position: absolute;
        top: auto;
        width: 80px;
    }

    //&::after {
    //    content: 'a ';
    //    position: relative;
    //    top: 0;
    //    left: 0;
    //    width: 50px;
    //    height: 50px;
    //    background-color: green;
    //    border: 1px solid blue;
    //    //opacity: 50%;
    //
    //}

    button.close {
        &:focus, span:focus {
            background-color: transparent;
            border-color: darken($black, 10%);
            box-shadow: 0 0 0 0.2rem rgba($black, 0.2);
            outline: 0;
            padding-bottom: 1.5px;
        }
    }
    .close {
        line-height: 12px;
        margin-left: 16px;
        margin-top: 4px;
        opacity: 1;
        text-shadow: none;
    }
    &.no-icon {
        padding: calc-rem(20px) calc-rem(24px);
        &:before {
            display: none;
        }
    }
}

@each $name, $color in $message-colors {
    .alert-#{$name} {
        @include themify($themes) {
            //$background-color: themed-component(#{$name + '-background-color'}, 'alerts');
            //$border-color: themed-component(#{$name + '-border-color'}, 'alerts');
            //$border-right-color: themed-component(#{$name + 'border-right-color'}, 'alerts');
            $background-color: $color;
            $border-color: $background-color;
            $color: test-contrast($background-color);

            @if index('orange' 'orange-dark' 'warning',$name) { $color: $cl-blue-dark; }

            background-color: $background-color;
            border-color: $border-color;
            //border-right: 4px solid $border-right-color;
            color: $color;
            overflow: hidden;

            &:before {
                content: themed-component(#{$name + '-icon'}, 'alerts');
                color: $color;
            }

            hr {
                border-top-color: darken($background-color, 5%);
            }

            .alert-link {
                color: $color;
                text-decoration: underline;
            }
            .close {
                color: $color;
            }
            .alert-icon {
                color: $color;
                opacity: 0.05;
            }
        }
    }
}
