@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.card {
	border-radius: 0;
    &.card-knockout {
	    border: none;
        &.card-header-border-alto { border-color: $alto; }

        .card-header {
            background-color: transparent;
            background-image: none;
            position: relative;
            border-bottom-width: 1px;
            border-bottom-style: solid;

            &.header-border-alto { border-color: $alto; }
        }

        .card-body {
            border: none;
            padding-top: 16px;
        }

        .card-footer {
            border: none;
            background-color: transparent;
        }

    }

    &.card-the-max {
        .card-header {
            padding-left: 0;
            padding-right: 0;
        }

        .card-body {
            padding-left: 0;
            padding-right: 0;
	        .separator {
		        margin-left: 0;
		        margin-right: 0;
	        }
        }

        .card-footer {
            padding-left: 0;
            padding-right: 0;
        }
    }

    &.equal-height { min-height: 100%; }

    .card-header {
        display: flex;
        align-items: center;
	    border-radius: 0;
        min-height: 64px;
        padding: 0 calc-rem(28px);
        font-family: $font-family-light;
        font-size: calc-rem(20px);
        line-height: calc-rem(20px);

        &:empty { display: none; }

        [class^='alert-'] {
            position: relative;
            left: -1px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 52px;
            height: 66px;
            margin: -1px 1rem -1px calc-rem(-29px);
            font-size: 20px;
            font-weight: normal;
            border-right-width: 2px;
            border-right-style: solid;

            &:before { font-family: FontAwesome; }

            &.alert-success {
                &:before { font-family: FontAwesome; }
            }

            &.alert-warning {
                &:before { font-family: FontAwesome; }
            }

            &.alert-info {
                &:before { font-family: FontAwesome; }
            }

            &.alert-danger {
                &:before { font-family: FontAwesome; }
            }
        }

        .collapse-heading {
            position: relative;
            width: 100%;
            padding-left: 20px;
            cursor: pointer;

            &:before {
                content: '\f107';
                position: absolute;
                top: 50%;
                left: 0;
                width: 10px;
                margin-top: -4px;
                font-family: FontAwesome;
                font-size: calc-rem(18px);
                line-height: 10px;
                transition: transform 300ms ease-in-out;
            }

            &.collapsed {
                &:before {
                    transform: rotate(-90deg);
                    transition: transform 300ms ease-in-out;
                }
            }
        }

        .toolbox {
            display: flex;
            align-items: center;
            flex-direction: row;
            margin-left: auto;

            .btn {
                margin-bottom: 0;
                margin-left: 6px;
            }

            .icon {
                margin: 2px 4px 0 0;
                font-size: calc-rem(18px);
                line-height: calc-rem(20px);
            }
        }
    }

    .card-body {
        padding: calc-rem(28px);

        .separator {
            margin-left: calc-rem(-28px);
            margin-right: calc-rem(-28px);
            border-top-width: 1px;
            border-top-style: solid;
        }
    }

    .card-footer {
	    border-radius: 0;
    }

    &.card-inverted {
        .card-header { border-bottom: 0; }
    }


    //- Theming ========================================================================================================
    @include themify($themes) {
        background: themed-component('background-color', 'cards');
        border-color: themed-component('border-color', 'cards');

        &.card-knockout {
            @each $name, $gradient-color in $bgGradientColor {
                &.card-#{$name}, &.card-default {
                    .card-header {
                        background-image: none;
                        color: themed-component('card-color', 'cards');
                        border-bottom-color: themed-component('card-header-border-bottom-color', 'cards');

                        .card-body { color: themed-component('knockout-body-color', 'cards'); }
                    }
                }
            }
            //.card-header { border-color: themed-component('knockout-header-border-color', 'cards'); }
        }

        .card-header {
            $bg-color:  themed-component('card-header-background-color', 'cards');
            $bg-border-color: themed-component('card-border-color', 'cards');
            $color: themed-component('card-header-color', 'cards');
            $alert-br-color-default: themed-component('card-header-alert-border-right-color-gradient', 'cards');

            background-image: none;
            background-color: $bg-color;
            border-color: $bg-border-color;
            color: $color;

            &.hide-bg-image { background-image: none; }

            &.fa { color: themed-component('card-header-icon-color', 'cards'); }

            [class^='alert-'] {
                border-right-color: $alert-br-color-default;
                background-color: themed-component('alert-default-background-color', 'cards');

                &:before { content: themed-component('alert-default-icon', 'cards'); }

                &.alert-success {
                    &:before { content: themed-component('success-icon', 'alerts'); }
                }

                &.alert-warning {
                    &:before { content: themed-component('warning-icon', 'alerts'); }
                }

                &.alert-info {
                    &:before { content: themed-component('info-icon', 'alerts'); }
                }

                &.alert-danger {
                    &:before { content: themed-component('danger-icon', 'alerts'); }
                }
            }


            @each $name, $color in $message-colors {
                $background-color: themed-component(#{$name + '-background-color'}, 'alerts');
                $color: test-contrast($background-color);

                .alert-#{$name} {
                    background-color: $background-color;
                    color: $color;
                }
            }

            .collapse-heading {
                color: themed-component('collapse-heading-color', 'cards');

                &:before { color: themed-component('collapse-heading-icon-color', 'cards'); }

                &:hover { color: themed-component('collpase-heading-hover-color', 'cards');

                    &:before { color: themed-component('collapse-heading-icon-hover-color', 'cards'); }
                }
            }
        }

        .card-body {
            border-color: themed-component('card-border-color', 'cards');

            .separator { border-color: themed-component('card-separator-border-color', 'cards'); }
        }

        .card-footer {
            background-color: themed-component('card-footer-background-color', 'cards');
            border-color: themed-component('card-border-color', 'cards');
            //border-top-color: themed-component('card-footer-border-top-color', 'cards');
            color: themed-component('card-footer-color', 'cards');
        }

        @each $name, $gradient-color in $bgGradientColor {
            &.card-#{$name} {
                .card-header {
                    background-color: $cl-transparent;
                    background-image: $gradient-color;
                    border-right-color: rgba($cl-white, 0.5);
                    color: $cl-white;

                    @if $name != 'orange' { color: $cl-white; }
                    @else { color: $cl-blue-dark }

                    [class^='alert-'] {
                        border-right-color: themed-component('card-header-alert-border-right-color-gradient', 'cards');
                    }

                    .collapse-heading, .collapse-heading::before {
                        color: inherit;
                    }

                    .toolbox {
                        .btn { }

	                    .icon {
		                    color: inherit;
	                    }
                    }
                }
            }
        }

        &.card-light {
            border-color: themed-component('card-light-border-color', 'cards');

            .card-header {
                background: themed-component('card-light-header-background-color', 'cards') none;
                border-bottom-color: themed-component('card-light-header-border-bottom-color', 'cards');
                color: themed-component('card-light-header-color', 'cards');

                .heading {
                    color: themed-component('card-light-header-color', 'cards');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.card-img, .card-img-top, .card-img-bottom {
    border-radius: 0;
}

.accordion-group {
    @include themify($themes) {
        > .card { margin-bottom: 1px;

            &:last-child {
                //border-bottom: 1px solid themed-component('accordion-group-last-card-border-bottom-color', 'cards');
            }
        }
    }
}
