@import '../variables';
@import '../mixins/mixins';

.gradient-blue-bg {
    position: relative;
    &:before {
        background-color: rgba($primary, 0.7);
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        content: '';
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
}



.layout-full-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 100%;
    .layout-full-page-content {
        align-items: center;
        background: #007faa no-repeat center;
        background-size: cover;
        display: flex;
        height: calc(100vh - 197px);
        overflow: hidden;
        padding: 60px 0 70px;
        text-shadow: 0 0 1px rgba(0, 0, 0, 0.9);
    }
}



.layout-banner-bg {
    align-items: center;
    background: #007faa no-repeat center;
    background-size: cover;
    display: flex;
    height: 75vh;
    overflow: hidden;
    padding: 60px 0 70px;
    &.short {
        background-position: bottom center;
        background-size: 100% auto;
        display: flex;
        flex-direction: row;
        min-height: 100%;
        height: 250px;
        padding: 0;
        position: relative;
        width: 100%;
        text-shadow: 0 0 1px rgba(0, 0, 0, 0.9);
    }
    @media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
        &.short {
            background-size: cover;
            height: 175px;
            padding: 1rem;
            margin-bottom: 1rem;
        }
    }
}
.layout-banner-content {
    z-index: 1;

    @media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
        .avatar {
            min-height: 58px;
            min-width: 60px;
            height: 58px;
            width: 60px;
        }
        h1 {
            font-size: 2rem;
            line-height: 2.5rem;
        }
        h2 {
            font-size: 1rem;
            line-height: 1.25rem;
        }
    }
}



.layout-search-property {
    .search-property-bg {
        align-items: center;
        background: #007faa no-repeat center;
        background-size: cover;
        display: flex;
        height: 75vh;
        overflow: hidden;
        padding: 60px 0 70px;
    }
    footer {
        background: $white;
    }
}

// Login
.layout-login {
    .cl-main-nav {
        border-bottom: 0;
        &.navbar-light, &.navbar-dark {
            .navbar-logo {
                border-color: transparent;
            }
        }
    }
    .footer-light {
        background-color: $white;
        padding: 40px 0;
    }
    .footer-dark {
        padding: 40px 0;
    }
}
.login-bg {
    align-items: center;
    background: $primary no-repeat center;
    background-size: cover;
    color: $white;
    display: flex;
    padding: 60px 0 70px;
}


// Do not use this class, .use layout-banner-bg instead
.profile-banner {
    align-items: center;
    background: #007faa no-repeat center;
    background-size: cover;
    display: flex;
    height: 75vh;
    overflow: hidden;
    padding: 60px 0 70px;
}