@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.sidebar-nav {
    display: flex;
    width: 100%;
    .nav-heading {
        padding: 0 32px;
    }
    > .nav {
        flex-direction: column;
        width: 100%;
        .nav-divider {
            height: 1px;
            margin: 12px 0;
            width: 100%;
        }
        .nav-link {
            font-size: calc-rem(16px);
            line-height: 20px;
            padding: 9px 32px;
            width: 100%;

            > .fa {
                font-size: 11px;
            }
            &.active {
                font-family: $font-family-book;
                position: relative;
                @if $showTextShadows == true {
                    text-shadow: 0 0 1px rgba($black, 0.9);
                }
                small {
                    color: $gallery !important;
                }
            }
        }
        > .sidebar-nav-subnav {
            width: 100%;
            > .nav-link-collapse {
                padding-left: 32px;
                position: relative;
                &:before {
                    content: '\f107';
                    font-family: FontAwesome;
                    font-size: calc-rem(16px);
                    left: 16px;
                    line-height: 10px;
                    margin-top: -4px;
                    position: absolute;
                    top: 50%;
                    transition: transform 300ms ease-in-out;
                    width: 10px;
                }
                &.collapsed {
                    font-family: $font-family-light;
                    &:before {
                        transform: rotate(-90deg);
                        transition: transform 300ms ease-in-out;
                    }
                }
                &.active {
                    font-family: $font-family-book;
                }
                & ~ div {
                    .nav {
                        .nav-link {
                            line-height: 14px;
                            padding-left: 48px;
                        }
                    }
                }
            }
            > .nav {
                flex-direction: column;
                width: 100%;
                .nav-link {
                    padding-left: 50px;
                }
            }

            .sidebar-nav-subnav-children {
                width: 100%;
                > .nav-link-collapse {
                    padding-left: 48px;
                    position: relative;
                    &:before {
                        content: '\f107';
                        font-family: FontAwesome;
                        font-size: calc-rem(16px);
                        left: 32px;
                        line-height: 10px;
                        margin-top: -4px;
                        position: absolute;
                        top: 50%;
                        transition: transform 300ms ease-in-out;
                        width: 10px;
                    }
                    &.collapsed {
                        font-family: $font-family-light;
                        &:before {
                            transform: rotate(-90deg);
                            transition: transform 300ms ease-in-out;
                        }
                    }
                    &.active {
                        font-family: $font-family-book;
                    }
                    & ~ div {
                        .nav {
                            .nav-link {
                                line-height: 14px;
                                padding-left: 60px;
                            }
                        }
                    }
                }
                .nav {
                    flex-direction: column;
                    width: 100%;
                    > .nav-link {
                        padding-left: 50px;
                    }
                }
            }
        }
    }

    //- Theming ========================================================================================================
    @include themify($themes) {
        > .nav {
            .nav-divider { background-color: themed-component('nav-sidebar-light-divider-bg-color', 'nav-sidebar'); }

            .nav-link {
                color: themed-component('nav-sidebar-light-link-color', 'nav-sidebar');

                &:hover {
                   background-color: themed-component('nav-sidebar-light-link-hover-bg-color', 'nav-sidebar');
                   color: themed-component('nav-sidebar-light-link-hover-color', 'nav-sidebar');
                }

                &.active {
                    background-color: themed-component('nav-sidebar-light-link-active-bg-color', 'nav-sidebar');
                    color: themed-component('nav-sidebar-light-link-active-color', 'nav-sidebar');

                    &:hover { color: themed-component('nav-sidebar-light-link-active-hover-cover', 'nav-sidebar'); }
                }
            }

            > .sidebar-nav-subnav {
                > .nav-link-collapse {
                    color: themed-component('nav-sidebar-light-link-collapse-color', 'nav-sidebar');

                    &:hover {
                        color: themed-component('nav-sidebar-light-link-hover-color', 'nav-sidebar');

                        &:before { color: themed-component('nav-sidebar-light-link-arrow-hover-color', 'nav-sidebar'); }
                    }

                    &:before { color: themed-component('nav-sidebar-light-link-arrow-color', 'nav-sidebar'); }

                    &.collapsed {
                        color: themed-component('nav-sidebar-light-link-collapsed-color', 'nav-sidebar');

                        &:hover { color: themed-component('nav-sidebar-light-link-hover-color', 'nav-sidebar'); }
                    }
                }

                .sidebar-nav-subnav-children {
                    > .nav-link-collapse {
                        &:hover {
                            color: themed-component('nav-sidebar-light-link-hover-color', 'nav-sidebar');

                            &:before { color: themed-component('nav-sidebar-light-link-arrow-hover-color', 'nav-sidebar'); }
                        }

                        &:before { color: themed-component('nav-sidebar-light-link-children-collapse-color', 'nav-sidebar');}

                        &.collapsed { color: themed-component('nav-sidebar-light-link-children-collapsed-color', 'nav-sidebar');
                            &:hover { color: themed-component('nav-sidebar-light-link-hover-color', 'nav-sidebar'); }
                        }
                    }
                }
            }
        }

        &.sidebar-dark {
            > .nav {
                .nav-divider { background-color: themed-component('nav-sidebar-dark-divider-bg-color', 'nav-sidebar'); }

                .nav-link {
                    color: themed-component('nav-sidebar-dark-link-color', 'nav-sidebar');

                    &:hover {
                        background-color: themed-component('nav-sidebar-dark-link-hover-bg-color', 'nav-sidebar');
                        color: themed-component('nav-sidebar-dark-link-hover-color', 'nav-sidebar');
                    }

                    &.active {
                        background-color: themed-component('nav-sidebar-dark-link-active-bg-color', 'nav-sidebar');
                        color: themed-component('nav-sidebar-dark-link-active-color', 'nav-sidebar');

                        &:hover {
                            color: themed-component('nav-sidebar-dark-link-active-hover-color', 'nav-sidebar');
                        }
                    }
                }

                > .sidebar-nav-subnav {
                    > .nav-link-collapse {
                        color: themed-component('nav-sidebar-dark-link-collapse-color', 'nav-sidebar');

                        &:hover {
                            color: themed-component('nav-sidebar-dark-link-hover-color', 'nav-sidebar');

                            &:before { color: themed-component('nav-sidebar-dark-link-arrow-hover-color', 'nav-sidebar'); }
                        }

                        //&.active {
                        //    background-color: themed-component('nav-sidebar-dark-link-active-bg-color', 'nav-sidebar');
                        //    //color: themed-component('nav-sidebar-dark-link-collapse-color', 'nav-sidebar');
                        //    color: test-contrast(themed-component('nav-sidebar-dark-link-active-bg-color', 'nav-sidebar'));
                        //}

                        &:before { color: themed-component('nav-sidebar-dark-link-arrow-color', 'nav-sidebar'); }

                        &.collapsed {
                            color: themed-component('nav-sidebar-dark-link-collapsed-color', 'nav-sidebar');

                            &:hover { color: themed-component('nav-sidebar-dark-link-hover-color', 'nav-sidebar'); }
                        }
                    }

                    .sidebar-nav-subnav-children {
                        > .nav-link-collapse {
                            &:hover {
                                color: themed-component('nav-sidebar-dark-link-hover-color', 'nav-sidebar');

                                &:before { color: themed-component('nav-sidebar-dark-link-arrow-hover-color', 'nav-sidebar'); }
                            }

                            &:before { color: themed-component('nav-sidebar-dark-link-children-collapse-color', 'nav-sidebar');}

                            &.collapsed {
                                color: themed-component('nav-sidebar-dark-link-children-collapsed-color', 'nav-sidebar');

                                &:hover { color: themed-component('nav-sidebar-dark-link-hover-color', 'nav-sidebar'); }
                            }
                        }
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}