@import '../variables';

@mixin form-labels {
    @include themify($themes) {
        color: themed-component('label-color', 'forms');
    }
    display: inline-block;
    font-family: $font-family-book;
    font-size: 1rem;
    font-weight: normal;
    line-height: 1.5;
    margin-bottom: 0.25rem;
}

@mixin form-validation-state($state, $color) {

    .form-control,
    .custom-select {
        .was-validated &:#{$state},
        &.is-#{$state} {
            border-color: $color;

            &:focus {
                box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 6px lighten($color, 20%);
            }

            ~ .invalid-feedback,
            ~ .invalid-tooltip {
                display: block;
            }
        }
    }

    .form-check-input {
        .was-validated &:#{$state},
        &.is-#{$state} {
            + .form-check-label {
                color: $color;
            }
        }
    }

    // custom radios and checks
    .custom-control-input {
        .was-validated &:#{$state},
        &.is-#{$state} {
            ~ .custom-control-indicator {
                background-color: rgba($color, 0.25);
            }
            ~ .custom-control-description {
                color: $color;
            }
        }
    }

    // custom file
    .custom-file-input {
        .was-validated &:#{$state},
        &.is-#{$state} {
            ~ .custom-file-control {
                border-color: $color;

                &::before { border-color: inherit; }
            }
            &:focus {
                box-shadow: 0 0 0 0.2rem rgba($color, 0.25);
            }
        }
    }
}
