@import '../variables';
@import '../mixins/mixins';
@import '../themes';

#alertContainer {
    left: 10%;
    position: absolute;
    right: 10%;
    top: 80px;
    z-index: 9999;
    &.bottom {
        bottom: 15px;
        top: auto;
    }
    .alert {
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.55);
        margin-bottom: 0.5rem;
        &:first-child {
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
        }
    }

    @include themify($themes) {
        .alert {
            box-shadow: themed-component('global-alert-box-shadow', 'alerts');
        }
    }
}