@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.steps {
    display: flex;
    flex-direction: row;
    padding-top: 40px;
    .step {
        display: flex;
        flex: 1;
        flex-direction: column;
        position: relative;
        .step-badge {
            align-items: center;
            border-radius: 50%;
            display: flex;
            height: 41px;
            justify-content: center;
            width: 41px;
        }
        .step-title {
            bottom: 45px;
            font-family: $font-family-book;
            font-size: 13px;
            left: -40px;
            line-height: 15px;
            position: absolute;
            text-align: center;
            width: 120px;
        }
        .step-track {
            height: 5px;
            left: 0;
            position: absolute;
            right: 0;
            top: 18px;
            z-index: -1;
        }
        &:first-child {
            .step-title {
                left: 0;
                right: auto;
                text-align: left;
                width: 90px;
            }
        }
        &:last-child {
            flex: 0;
            .step-title {
                left: -50px;
                right: 0;
                text-align: right;
                width: 90px;
            }
        }
        &.completed-step {
            .step-badge {
                position: relative;
                &:after {
                    align-content: center;
                    content: '\f00c';
                    display: flex;
                    font-family: FontAwesome;
                    font-size: 22px;
                    height: 25px;
                    justify-content: center;
                    left: 50%;
                    margin-left: -12.5px;
                    margin-top: -12.5px;
                    position: absolute;
                    top: 50%;
                    width: 25px;
                }
            }
        }
        &.current-step {
            .step-badge {
                position: relative;
            }
        }
        &.current-step-arrow {
            &:after {
                border-style: solid;
                border-width: 12px 12px 12px 0;
                content: '';
                height: 12px;
                position: absolute;
                top: 44px;
                left: 14px;
                transform: rotate(90deg);
                width: 12px;
            }
        }
    }
    &.steps-icons {
        .step {
            .step-badge {
                .step-icon {
                    font-size: 18px;
                }
            }
            &.completed-step {
                .step-badge {
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .step {
            .step-badge {
                background-color: themed-component('step-badge-background-color', 'steps');
            }
            .step-title {
                color: themed-component('step-title-color', 'steps');
            }
            .step-track {
                background-color: themed-component('step-track-background-color', 'steps');
            }
            &.completed-step {
                .step-badge {
                    background-color: themed-component('step-badge-completed-background-color', 'steps');
                    border: themed-component('step-badge-completed-border', 'steps');
                    &:after {
                        color: themed-component('step-badge-completed-color', 'steps');
                    }
                }
                .step-title {
                    color: themed-component('step-title-completed-color', 'steps');
                }
            }
            &.completed-path {
                .step-track {
                    background-color: themed-component('step-track-completed-background-color', 'steps');
                }
            }
            &.current-step {
                .step-badge {
                    background-color: themed-component('step-badge-current-background-color', 'steps');
                    border: themed-component('step-badge-current-border', 'steps');
                }
                .step-title {
                    color: themed-component('step-title-current-color', 'steps');
                }
            }
            &.current-step-arrow {
                &:after {
                    border-color: themed-component('current-step-arrow-border', 'steps');
                }
            }
        }
        &.steps-icons {
            .step {
                .step-badge {
                    background-color: themed-component('step-icon-badge-background-color', 'steps');
                    border: themed-component('step-icon-badge-border', 'steps');
                    .step-icon {
                        color: themed-component('step-icon-badge-color', 'steps');
                    }
                }
                &.completed-step {
                    .step-badge {
                        border: themed-component('step-icon-completed-badge-border', 'steps');
                        .step-icon {
                            color: themed-component('step-icon-completed-color', 'steps');
                        }
                    }
                }
                &.current-step {
                    .step-badge {
                        border: themed-component('step-icon-current-badge-border', 'steps');
                        .step-icon {
                            color: themed-component('step-icon-current-badge-color', 'steps');
                        }
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.steps-banner {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    padding: 1.1rem;
    position: relative;

    .banner-info {
        &:last-child {
            text-align: right;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('step-banner-background-color', 'steps');
        color: themed-component('step-banner-color', 'steps');
        .banner-info {
            h2 {
                color: themed-component('step-banner-info-color', 'steps');
            }
        }
    }
    //- Theming ========================================================================================================
}
