@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.badge {
    display: inline-flex;
    font-family: $font-family-book;
    font-weight: normal;
    padding: 5px 11px 4px;
    position: relative;
    text-shadow: none;
    vertical-align: middle;
    //line-height: 13px;

    .fa {
        font-size: 92%;
        padding-left: 3px;
        // padding-top: 1px;
    }

    &.badge-medium {
        font-size: 85%;
        padding: 0.3rem 0.6rem;
    }

	& + .badge {
		margin-left: 6px;
	}

    @include themify($themes) {
        $merged: map-merge($all-colors, $new-brand-colors19);
        @each $name, $color in $merged {
            &.badge-#{$name} {
                $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                background-color: $background-color;

                @if index('orange' 'orange-dark' 'warning',$name) { color: $cl-blue-dark; }
                @else { color: test-contrast($background-color); }

            }
        }
    }
}

h1, .h1 {
    .badge {
        bottom: 3px;
        font-family: $font-family-book;
        font-size: calc(100% - 10px);
        margin-left: 1rem;
    }
}
h2, .h2, h3 .h3, h4, .h4, h5, .h5, h6, .h6 {
    .badge {
        align-items: center;
        font-family: $font-family-book;
        font-size: calc(100% - 4px);
        margin-left: calc-rem(4px);
        vertical-align: middle;
        .fa {
	        padding-left: 0;
        }
    }
}

