@import '../variables';
@import '../mixins/mixins';
@import '../themes';

footer, .footer {
    border-radius: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 18px 10px;

    .footer-logo {
        align-self: center;
        .clgx-logo {
            height: 80px;
            margin-right: 22px;
            width: 90px;
        }
    }
    .footer-nav {
        align-self: center;
        padding-right: 8px;
        .nav {
            line-height: calc-rem(18px);
        }
        .nav-link {
            font-size: calc-rem(13px);
            padding: calc-rem(6px) calc-rem(10px);
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
        .nav-divider {
            align-self: center;
            display: flex;
            height: 13px;
            margin-left: 0;
            margin-right: 0;
            width: 1px;
        }
        .footer-date {
            font-size: calc-rem(13px);
            padding-right: calc-rem(10px);
            text-align: right;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('footer-light-background-color', 'footer');

        .footer-nav {
            .nav-divider {
                background-color: themed-component('footer-light-nav-divider', 'footer');
            }
            .footer-date {
                color: themed-component('footer-light-date-color', 'footer');
            }
        }
        &.footer-dark {
            background-color: themed-component('footer-dark-background-color', 'footer');
            $footer-hide-logo: themed-component('footer-hide-logo', 'footer');
            $footer-dark-show-color-logo: themed-component('footer-dark-show-color-logo', 'footer');
            $footer-dark-show-white-logo: themed-component('footer-dark-show-white-logo', 'footer');

            .footer-logo {
                .clgx-logo {
                    @if $footer-dark-show-color-logo == true {
                        background: themed-component('clgx-logo-color', 'logo');
                    }
                    @if $footer-dark-show-white-logo == true {
                        background: themed-component('clgx-logo-white', 'logo');
                    }
                    @if $footer-hide-logo == true {
                        background: none;
                    }
                    background-size: 53px 43px;
                }
            }

            .footer-nav {
                .nav-link {
                    color: themed-component('footer-dark-nav-color', 'footer');
                    &.disabled {
                        color: themed-component('footer-dark-nav-disabled-color', 'footer');
                        &:hover {
                            color: themed-component('footer-dark-nav-disabled-color', 'footer');
                        }
                    }
                    &:hover {
                        color: themed-component('footer-dark-nav-hover-color', 'footer');
                    }
                }

                .nav-divider {
                    background-color: themed-component('foooter-dark-nav-divider', 'footer');
                }

                .footer-date {
                    color: themed-component('footer-dark-date-color', 'footer');
                }
            }
        }

        &.footer-light {
            $background-color: themed-component('footer-light-background-color', 'footer');
            $footer-hide-logo: themed-component('footer-hide-logo', 'footer');
            $footer-light-show-color-logo: themed-component('footer-light-show-color-logo', 'footer');
            $footer-light-show-white-logo: themed-component('footer-light-show-white-logo', 'footer');

            .footer-logo{
                .clgx-logo {
                    @if $footer-light-show-color-logo == true {
                        background: themed-component('clgx-logo-color', 'logo');
                    }
                    @if $footer-light-show-white-logo == true {
                        background: themed-component('clgx-logo-white', 'logo');
                    }
                    @if $footer-hide-logo == true {
                        background: none;
                    }
                    background-size: 53px 43px;
                }
            }
            
            .footer-nav {
                .nav-link {
                    color: themed-component('footer-light-nav-color', 'footer');
                    &:hover {
                        color: themed-component('footer-light-nav-hover-color', 'footer');
                    }
                }
            }
        }

        &.footer-white {
            background-color: themed-component('footer-white-background-color', 'footer');
            $footer-hide-logo: themed-component('footer-hide-logo', 'footer');

            .footer-logo {
                .clgx-logo {
                    @if $footer-hide-logo == true {
                        background: none;
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.sticky-footer {
    margin-bottom: 81px;
    footer, .footer {
        bottom: 0;
        position: absolute;
        width: 100%;
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'md')) {
    footer, .footer {
        flex-direction: column;
        justify-content: center;
        padding: 8px 10px;
        .footer-logo {
            .clgx-logo {
                margin-right: 0;
            }
        }
        .footer-nav {
            .nav {
                justify-content: center;
                line-height: calc-rem(15px);
            }
            .nav-link {
                font-size: calc-rem(12px);
                padding: 0 calc-rem(10px);
            }
            .footer-date {
                font-size: calc-rem(11px);
                padding-bottom: 16px;
                padding-top: 12px;
                text-align: center;
            }
        }
    }
}