@import '../variables';
@import '../mixins/mixins';

.notification {
    align-items: center;
    display: flex;
    justify-content: center;
    position: relative;
    width: 26px;

    .unread-items {
        background-color: $gray;
        border-radius: 50%;
        content: '';
        height: 9px;
        margin-right: -11px;
        position: absolute;
        right: 50%;
        top: 1px;
        width: 9px;

        &.count {
            align-items: center;
            display: flex;
            justify-content: center;
            background-color: $gray;
            border-radius: 26px;
            position: absolute;
            right: 50%;
            margin-right: -14px;
            top: -1px;
            font-size: 10px;
            line-height: 9px;
            font-family: $font-family-book;
            min-width: 13px;
            min-height: 13px;
            padding: 2px;
            table-layout: fixed;
            color: $white;
        }

//- Theming ========================================================================================================
        $merged-colors: map-merge($all-colors, $theme-colors);
        $merged-colors: map-merge($merged-colors, $new-brand-colors19);

        @each $name, $color in $merged-colors {
            &.notify-#{$name} {
                @include themify($themes) {
                    $background-color: $color;
                    $text-color: test-contrast($color);

                    @if index('orange' 'orange-dark' 'warning' 'amethyst',$name) { $text-color: $cl-blue-dark; }

                    background-color: $background-color;
                    color: $text-color;
                }
            }
        }
    }
}

