@import '../variables';

.table-command-area {
    margin-bottom: 6px;
    display: flex;
    flex-direction: row;
    .btn, .dropdown, .form-group {
        margin-left: 6px;
        &:first-child {
            margin-left: 0;
        }
    }
    .form-group {
        margin-bottom: 0;
    }
}

.command-toolbox {
    display: flex;
}

.command-accessories {
    display: flex;
    flex: 1 1 auto;
    margin-right: 6px;
    margin-left: 6px;
    .form-group {
        display: flex;
        flex: 1;
        .input-group {
            display: flex;
            flex: 1 1 auto;
            height: auto;
            .form-control {
                border-color: transparent;
                height: auto;
            }
        }
    }
}

@media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
    .table-command-area {
        align-items: flex-start;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        .command-toolbox {
            order: 1;
            .btn, .dropdown {
                margin-right: 6px;
            }
        }
        .command-accessories {
            order: 2;
            width: 100%;
        }
        .btn, .dropdown {
            display: inline;
            margin: 0 0 6px 0;
            &:last-child {
                margin-bottom: 0;
            }
        }
        .form-group {
            margin: 6px 0;
        }
    }
}