@import '../variables';
@import '../mixins/mixins';
@import '../themes';
@import '../functions';

@mixin list-group-variant($color) {
    $text-color: darken($color, 0.5);
    $background-color: transparentize($color, 0.88);
    $border: lighten($color, 0.4);

    background-color: $background-color;
    //border-color: $color;
    color: $text-color;
}

.list-group-item {
    &:first-child {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
    &:last-child {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

}

@include themify($themes, true) {
    .list-group-item {
        background-color: themed-component('background-color', 'list-group');
        border-color: themed-component('border-color', 'list-group');
        color: themed-component('color', 'list-group');
        @each $name, $colors  in $all-colors {
            $color: nth($colors, 1);
            &.list-group-item-#{$name} {
                @include list-group-variant($color);
            }
        }
    }
}

