@import '../mixins/mixins';

.cl-keyvalue {
    font-size: calc-rem(16px);
    .cl-key {
        font-family: $font-family-book;
        font-size: calc-rem(16px);
        font-weight: 400;
    }

    .cl-value {
        line-height: 1;
    }

    &.inline .cl-key, &.inline .cl-value {
        display: inline-block;
    }

    &.inline .cl-key {
        padding-right: 6px;

        &:after {
            content: ':';
        }
    }
}