@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.nav-pills {
    padding-bottom: 7px;
    .nav-item {
        margin-right: 4px;
        .nav-link {
            background-color: rgba($seashell, 0.9);
            border-radius: 0;
            font-family: $font-family-book;
            margin-right: 4px;
            padding: 0.75rem calc-rem(26px) 0.625rem;

            &.active {
                &:after {
                    display: none;
                }
            }
        }
    }

    .nav-link.active,
    .nav-item.show .nav-link {
        background-color: $primary;
        border-color: $alto $alto $white;
        color: $white;
        font-family: $font-family-book;
        &:hover, &:focus {
            background-color: darken($primary, 2%);
        }
    }

    @include themify($themes) {
        .nav-item {
            .nav-link {
                background-color: themed-component('background-color', 'nav-pills');
                color: themed-component('color', 'nav-pills');
                &:hover, &:focus {
                    background-color: themed-component('hover-background-color', 'nav-pills');
                }
            }
        }
        .nav-link.active,
        .nav-item.show .nav-link {
            background-color: themed-component('active-background-color', 'nav-pills');
            border-color: themed-component('active-border-color', 'nav-pills');
            color: themed-component('active-color', 'nav-pills');
            font-family: $font-family-book;
            &:hover, &:focus {
                background-color: darken(themed-component('active-background-color', 'nav-pills'), 2%);
            }
        }
    }
}