@import '../variables';

@mixin navbar-variant($gradStart, $gradEnd, $background) {
    background: $background linear-gradient(to bottom, $gradStart, $gradEnd);
}
@mixin navbar-search-notempty {
    background-color: $white;
    color: $dark-gray;
    font-style: normal;
}

@mixin miniMainNav {
    padding-right: 0;
    .navbar-brand {
        font-size: 16px;
        padding-right: 16px;
    }
    .navbar-logo {
        height: 50px;
        width: 60px;
    }
    .navbar-collapse {
        .navbar-nav {
            .nav-link {
                font-size: calc-rem(14px);
                padding: 6px 0;
            }
        }
        .nav {
            padding: 8px 0;
            .nav-link {
                padding: 6px 0;
            }
        }
        .navbar-search {
            padding: 20px 20px 0;
            .input-icon {
                color: $clBlack;
            }
        }
    }
}

@mixin nextSizeDownNav {
    &.cl-main-nav {
        padding-right: 0;
        .navbar-brand {
            font-size: 16px;
            margin-right: 16px;
        }
        .navbar-logo {
            height: 70px;
            width: 80px;
        }
        .navbar-nav {
            .nav-divider {
                display: none;
            }
        }

        .navbar-collapse {
            .nav-item {
                padding: 6px 20px;
                &.notification {
                    .unread-items {
                        top: 11px;
                    }
                }
            }
        }
        .navbar-search {
            margin-top: 20px;
            .form-group {
                display: flex;
            }
        }
        .nav {
            display: inherit;
            .nav-item {
                margin-left: -5px;
                &.dropdown {
                    // padding: 0 2px;
                    .dropdown-menu {
                        &.dropdown-menu-right {
                            left: 10px;
                            right: auto;
                        }
                    }
                }
            }
            .nav-divider {
                display: none;
            }
            .navbar-search-toggle {
                display: none;
            }
        }
        .navbar-toggler {
            .nav {
                .nav-link {
                    color: $white;
                    font-size: 20px;
                    &:hover, &:active, &:focus {
                        color: $white;
                    }
                }
            }
        }
        &.navbar-light {
            .navbar-toggler {
                .nav {
                    .nav-link {
                        color: $black;
                        &:hover, &:active, &:focus {
                            color: $black;
                        }
                    }
                }
            }
        }
    }
}

@mixin mobile-main-nav {
    .navbar-collapse {
        background-color: $cl-gray-dark;
        border-top: 1px solid rgba($white, 0.12);
        border-bottom: 1px solid rgba($white, 0.2);
        box-shadow: inset 0px -2px 6px 0px rgba($black, 0.2);
        .nav-item {
            &.dropdown {
                flex-direction: column;
                .dropdown-menu {
                    background-color: transparent;
                    border: 0;
                    box-shadow: none;
                    color: $white;
                    display: block;
                    float: none;
                    left: auto;
                    margin: 0;
                    min-width: 0;
                    padding: 0;
                    position: relative;
                    right: auto;
                    top: auto;
                    .dropdown-item {
                        color: $white;
                        padding-left: 48px;
                        &:hover {
                            background-color: transparent;
                            color: #cccccc;
                        }
                        &:focus {
                            background-color: transparent;
                            color: $white;
                            &:hover {
                                background-color: transparent;
                                color: $white;
                            }
                        }
                    }
                    .dropdown-divider {
                        display: none;

                    }
                }
            }
            &.notification {
                display: none;
            }
        }
        .navbar-nav {
            padding: 12px 0;
            .nav-link {
                font-size: calc-rem(16px);
                padding: 6px 20px;
                color: $white;
                &:hover {
                    color: $white;
                }
	            &.active {
		            &:after {
			            display: none;
		            }
	            }
                &.secondary-nav-mobile {
                    display: inline-block;
                    padding-left: 46px;
                }
            }
            .nav-divider {
                display: none;
            }
        }
        .nav {
            flex-direction: column;
            padding: 8px 0;
            .nav-link {
                align-items: center;
                display: flex;
                flex-direction: row;
                padding: 6px 20px;
                color: $white;
                &:hover {
                    color: $white;
                }
                &.dropdown-toggle {
                    @include dropdown-arrow-variant($white);
                }
                .fa {
                    margin-right: 6px;
                }
                &.navbar-search-toggle {
                    display: none;
                }
            }
        }

        .navbar-search {
            padding: 20px 20px 0;
            .form-control {
                margin-left: 0;
            }
            .input-icon {
                color: $clBlack;
            }
        }
    }

}
