@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.upload-container {
    align-items: center;
    border-radius: 0;
    display: flex;
    justify-content: center;
    min-height: 130px;
    padding: 27px 5px;
    text-align: center;

    input {
        &[type='file'] {
            display: none;
        }
    }

    .upload-components {
        .drag-message, .upload-progress {
            display: none;
        }
    }

    &.dragover {
        border-radius: 3px;
        font-size: $font-size-lg;
        font-weight: bold;
        padding: 88px 0;

        .upload-components {
            .fa-cloud-upload, .upload-message, .btn, .upload-progress {
                display: none;
            }

            .dragbox, .drag-message {
                display: block;
            }
        }
    }

    &.uploading {
        .upload-components {
            .fa-cloud-upload, .upload-message, .btn {
                display: none;
            }

            .dragbox, .drag-message {
                display: none;
            }

            .upload-progress {
                display: block;

                .filename {
                    font-weight: bold;
                    margin-bottom: 5px;
                }

                .uploading-message {
                    margin-top: 2px;
                }

                .progress {
                    margin: 15px auto 10px;
                    width: 180px;
                }
            }
        }
    }

    h2 {
        clear: both;
        display: block;
        font-size: $font-size-lg;
        line-height: 21px;
        margin: 0 auto;
    }

    .btn {
        margin-top: 15px;
    }

    .fa-cloud-upload {
        font-size: 45px;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        border: themed-component('border', 'drag-and-drop-file-upload');
        &.dragover {
            background-color: themed-component('dragover-background-color', 'drag-and-drop-file-upload');
            border: themed-component('dragover-border', 'drag-and-drop-file-upload');
            color: themed-component('dragover-color', 'drag-and-drop-file-upload');
        }
        .fa-cloud-upload {
            color: themed-component('fa-cloud-upload-color', 'drag-and-drop-file-upload');
        }
    }
    //- Theming ========================================================================================================
}