@import '../variables';
@import '../mixins/mixins';
@import '../themes';

input[type=range]::-moz-focus-outer {
    border: 0;
}

.cl-slider-input {
    -webkit-appearance: none;
    width: 100%;

    &:focus {
        @include set-focus;
    }

    &:disabled {
        opacity: 0.5;
        &::-webkit-slider-thumb {
            cursor: not-allowed;
        }
        &::-moz-range-thumb {
            cursor: not-allowed;
        }
        &::-ms-thumb {
            cursor: not-allowed;
        }
    }

    &::-webkit-slider-runnable-track {
        @include range-track;
    }
    &::-webkit-slider-thumb {
        @include range-thumb;
    }

    &::-moz-range-track {
        @include range-track;
    }
    &::-moz-range-thumb {
        @include range-thumb;
    }
    &::-moz-range-progress {
        @include range-lower;
        //height: 12px;
    }

    // styles the sliders individual tracks
    &::-ms-track {
        @include range-track;
        // margin: 9px 0 9px 0;
    }
    // controls styling of the tooltip of a slider
    &::-ms-tooltip {
        display: none;
    }
    // for the slider thumb itself
    &::-ms-thumb {
        @include range-thumb;
        margin-top: 0;
    }
    &::-ms-ticks-before {
        display: none;
    }
    &::-ms-ticks-after {
        display: none;
    }

    &.is-invalid {
        & ~ .invalid-feedback {
            display: block;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        &::-webkit-slider-runnable-track {
            box-shadow: themed-component('slider-track-box-shadow', 'slider');
        }

        &::-webkit-slider-thumb {
            background: themed-component('slider-thumb-color', 'slider');
            box-shadow: themed-component('slider-thumb-box-shadow', 'slider');
            &:active {
                box-shadow: themed-component('slider-thumb-active-box-shadow', 'slider');
            }
        }

        &::-moz-range-track {
            box-shadow: themed-component('slider-track-box-shadow', 'slider');
        }

        &::-moz-range-thumb {
            background: themed-component('slider-thumb-color', 'slider');
            box-shadow: themed-component('slider-thumb-box-shadow', 'slider');
            &:active {
                box-shadow: themed-component('slider-thumb-active-box-shadow', 'slider');
            }
        }

        &::-moz-range-progress {
            background: themed-component('slider-range-progress-background-color', 'slider');
        }
        // styles the sliders individual tracks
        &::-ms-track {
            color: themed-component('slider-color-transparent', 'slider');
        }
        // for the slider thumb itself
        &::-ms-thumb {
            background: themed-component('slider-thumb-color', 'slider');
            box-shadow: themed-component('slider-thumb-box-shadow', 'slider');
            &:active {
                box-shadow: themed-component('slider-thumb-active-box-shadow', 'slider');
            }
        }
        // &::-ms-fill-lower controls styling of the sliders track before the slider thumb
        &::-ms-fill-lower {
            background: themed-component('slider-color-transparent', 'slider');
        }
        // &::-ms-fill-upper same as above, but after the thumb
        &::-ms-fill-upper {
            background: themed-component('slider-color-transparent', 'slider');
        }

        @each $name, $color in $all-colors {
            $background-color: themed-component(#{$name + '-background-color'}, 'colors');
            background: themed-component('slider-color-transparent', 'slider');

            @if $name == 'default' {
                $background-color: themed-component('slider-default-background-color', 'slider');
                background: $background-color;
            }

            &.slider-#{$name} {
                &::-webkit-slider-runnable-track {
                    background: $background-color;
                }
                &::-moz-range-track {
                    background: $background-color;
                }
                &::-ms-track {
                    background: $background-color;
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.cl-slider-value {
    border-radius: $border-radius;
    display: inline-block;
    font-size: 14px;
    line-height: 16px;
    margin-left: 8px;
    margin-top: 1px;
    padding: 2px 4px 0;
    position: relative;
    text-align: center;
    vertical-align: top;
    width: 40px;

    &:after {
        content: '';
        height: 0;
        left: -7px;
        position: absolute;
        top: 2px;
        width: 0;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background: themed-component('slider-value-background-color', 'slider');
        color: themed-component('slider-value-color', 'slider');
        &:after {
            border-bottom: themed-component('slider-value-border-top-bottom', 'slider');
            border-right: themed-component('slider-value-border-right', 'slider');
            border-top: themed-component('slider-value-border-top-bottom', 'slider');
        }
    }
    //- Theming ========================================================================================================
}