@import '../variables';
@import 'forms';
@import '../themes';

@mixin form-control-base {
    background-clip: padding-box;
    display: block;
    font-size: calc-rem(16px);
    font-weight: 400;
    height: 38px;
    line-height: 1.25;
    padding: 0 12px;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    width: 100%;

    &:focus {
        @include set-focus;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background: themed-component('select-form-control-background', 'select');
        box-shadow: themed-component('select-form-control-box-shadow', 'select');
        border: themed-component('select-border', 'select');
        color: themed-component('select-color', 'select');
    }
    //- Theming ========================================================================================================
}

.custom-select, .hourselect, .minuteselect, .secondselect, .ampmselect {
    appearance: none;
    display: block;
    font-weight: $font-normal;
    position: relative;
    transition: border-color ease-in-out 0.15s, box-shadow 0.15s ease-in-out;
    width: 100%;

    &:focus:not(.is-invalid) {
        @include set-focus;
        outline: 0;
    }

    &:disabled {
        cursor: not-allowed;
        opacity: 0.65;
    }
    &.custom-select-sm {
        font-size: calc-rem(14px);
        line-height: 1.15;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background: themed-component('select-background', 'select');
	    background-size: 20px 20px;
        border: themed-component('select-border', 'select');
        color: themed-component('select-color', 'select');
        &:focus:not(.is-invalid) {
            border: themed-component('select-border', 'select');
        }
        &:disabled {
            background-color: themed-component('select-dropdown-disabled-background-color', 'select');
            color: themed-component('select-color', 'select');
        }
	    &:invalid, &.is-invalid {
		    background: themed-component('select-background', 'select');
		    background-size: 20px 20px;
		    appearance: none;
	    }
    }
    //- Theming ========================================================================================================
}

.cl-select-wrapper {
    cursor: pointer;
    display: block;
    position: relative;

    &.is-invalid {
        .dropdown {
	        border-radius: 0.25rem;
            outline-width: 0;
            transition: border-color ease-in-out 0.15s, box-shadow 0.15s ease-in-out;

            > button, .btn {
                border: 0;
                box-shadow: none;
                height: 36px;

                &:focus {
                    @include set-focus;
                    // box-shadow: 0 0 12px rgba($danger, 0.6);
                }
            }
        }

        .invalid-feedback {
            display: block;
        }
    }

    > .dropdown {
        > button, .btn {
            @include form-control-base;
            box-shadow: none;
            font-family: $font-family-light;
            margin-left: 0;
            overflow-x: hidden;
            text-align: left;
	        border-radius: 0.25rem;

            &:after {
                display: none;
            }

            &:disabled {
                cursor: not-allowed;
                background-color: $gallery;
                color: $black;
            }
        }

        > .dropdown-menu {
            // @include set-focus;
            border-radius: 0;
            width: 100%;

            .dropdown-item {
                white-space: normal;
                .text {
                    font-family: $font-family-book;
                }
                .text-desc {
                    font-size: calc-rem(14px);
                }
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        &.is-invalid {
            .dropdown {
                border: themed-component('select-invalid-dropdown-border', 'select');
                > .btn {
                    &:focus {
                        color: themed-component('select-invalid-dropdown-color', 'select');
                    }
                }
            }
        }
        > .dropdown {
            > button, .btn {
                background: themed-component('select-background', 'select');
                background-size: 20px 20px;
                color: themed-component('select-color', 'select');
            }
            &:disabled {
                background-color: themed-component('select-dropdown-disabled-background-color', 'select');
            }

            > .dropdown-menu {
                background: themed-component('select-dropdown-background-color', 'select');
                border-color: themed-component('select-border-color', 'select');
                .dropdown-item {
                    color: themed-component('select-color', 'select');
                    &:hover {
                        background: themed-component('select-dropdown-selected-background-color', 'select');
                        color: themed-component('select-dropdown-selected-color', 'select');
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

.form-inline {
    .custom-select {
        // border-radius: 0;
        display: inline-block;
        vertical-align: middle;
        width: auto;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        border-color: themed-component('select-border-color', 'select');
    }
    //- Theming ========================================================================================================
}

.input-group {
    select {
        height: auto;
    }
}
