@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.page-command-area {
    display: flex;
    flex-direction: row;
    height: 80px;
    padding: 0 40px;
    position: relative;
    width: 100%;
    min-width: 335px;

    .command-heading {
        align-self: center;
        display: flex;
        flex: 2;
        flex-direction: column;
        justify-content: center;
        .header {
            font-family: $font-family-light;
            font-size: calc-rem(28px);
            font-weight: normal;
            line-height: calc-rem(34px);
            white-space: nowrap;
            @if $showTextShadows == true {
                text-shadow: 0 0 1px rgba($black, 0.9);
            }
        }
        .sub-heading {
            display: flex;
            flex-direction: row;
            font-family: $font-family-light;
            font-size: calc-rem(14px);
            font-weight: 400;
            line-height: 15px;
            list-style: none;
            margin-bottom: 3px;
            @if $showTextShadows == true {
                text-shadow: 0 0 1px rgba($black, 0.9);
            }
            .sub-heading-divider {
                align-self: center;
                height: 15px;
                justify-items: center;
                margin-left: 16px;
                margin-right: 16px;
                width: 1px;
            }
        }
    }

    @include themify($themes) {
        $divider-color: themed-component('sub-heading-divider-color', 'page-command-area');
        $bg-color: themed-component('background-color', 'page-command-area');
        $text-color: themed-component('color', 'page-command-area');
        background-color: $bg-color;
        color: $text-color;

	    .command-heading {
		    color: $text-color;
		    .header { color: $text-color; }
	    }

	    .sub-heading {
		    color: $text-color;
		    .sub-heading-divider { background-color: $divider-color; }
	    }

		@each $name, $gradient-color in $gradientColor {
			@if $name != 'gray' {
				&.pca-#{$name} {
					$bg-img: $gradient-color;
					$color: $cl-white;

                    @if $name == 'orange' {
						$color: $cl-blue-dark;
					}

					background-color: $cl-transparent;
					color: $color;
					background-image: $bg-img;
					// border-bottom-color: themed-component('card-header-border-bottom-color', 'cards');

					.command-heading {
						color: $color;
						.header {
							color: $color;
						}
					}

					.sub-heading {
						color: $color;
						.sub-heading-divider {
							background-color: rgba($color, 0.5);
						}
					}
				}
			}
		}
    }

    .command-toolbox {
        align-self: center;
        display: flex;
        //flex: 1;
        justify-content: flex-end;
        z-index: 2;
        .btn {
            margin-left: 4px;
        }
    }

    @media screen and (max-width: map-get($grid-breakpoints, 'lg')) {
        background-position: right 0 center;
        flex-direction: row;

        .command-heading {
            margin-left: 3px;
            .header {
                font-size: calc-rem(22px);
                line-height: 24px;
            }
            .sub-heading {
                .sub-heading-divider {
                    height: 12px;
                }
            }
        }
    }


    @media screen and (max-width: map-get($grid-breakpoints, 'md')) {
        background-position: right 0 center;
        //height: 70px;
        .command-heading {
            .sub-heading {
                .sub-heading-divider {
                    height: 12px;
                }
            }
        }
        .command-toolbox {
            .btn {
                font-size: 0.875rem;
                padding: calc-rem(7px) calc-rem(8px) calc-rem(6px);
            }
        }
    }

    @media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
        background-position: right 0 center;
        .command-heading {
            .sub-heading {
                flex-direction: column;
                .sub-heading-divider {
                    display: none;
                }
            }
        }
    }
}
