.accumulator {
    .available, .selected {
        display: flex;
        .form-group {
            margin-bottom: 0;
            position: relative;
            width: 100%;
            .form-control {
                bottom: 0;
                left: 0;
                position: absolute;
                right: 0;
                top: 30px;
                width: 100%;
            }
        }
    }
    .transfer-column, .sort-column {
        justify-content: unset;
        margin-top: 27px;
        .btn {
            display: flex;
            justify-content: center;
            margin-top: 3px;
            .fa {
                font-size: 1.15rem;
                width: 11px;
                &:before {
                    text-align: center;
                }
            }
            &:nth-child(2) {
                margin-bottom: 10px;
            }
        }
    }
    .sort-column {
        margin-left: -10px;
        .btn {
            .fa {
                font-size: unset;
                width: auto;
            }
        }
    }
}