@import '../variables';
@import '../mixins/mixins';
@import '../themes';

$prefix-class: daterangepicker;

@mixin input-container {
    align-items: center;
    padding: 0;
    margin: 0;
    text-align: left;
    display: flex;
    flex-direction: row-reverse;
    //height: 64px;
    justify-content: flex-end;

    input {
        flex: 0 0 auto;
        width: 210px;
    }
    i {
        position: relative;
        left: auto;
        top: auto;
        width: 57px;
        display: flex;
        justify-content: center;
    }
    .fa {
        font-size: 28px;
        color: $alto;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('date-picker-input-background-color', 'datetime');
        border-top: themed-component('date-picker-input-border', 'datetime');
        //border-bottom: themed-component('date-picker-input-border', 'datetime');
    }
    //- Theming ========================================================================================================
}

.#{$prefix-class} {
    border-image-width: 0;
    border-radius: 0;
    font-family: $font-family-light;
    margin: 0;
    // width: 315px;
    padding: 0;

    &.dropdown-menu {
        border: 0;
        margin-top: 6px;
        max-width: none;
        padding: 0;
        z-index: 3001;
    }

    &:before, &:after {
        content: '';
        display: inline-block;
        position: absolute;
    }

    &:before {
        top: -12px;
    }

    &:after {
        top: -12px;
    }

    &.opensleft {
        &:before {
            right: 12px + 2px;
        }

        &:after {
            right: 12px + 4px;
        }
    }

    &.openscenter {
        &:before {
            left: 0;
            margin-left: auto;
            margin-right: auto;
            right: 0;
            width: 0;
        }

        &:after {
            left: 0;
            margin-left: auto;
            margin-right: auto;
            right: 0;
            width: 0;
        }
    }

    &.opensright {
        &:before {
            left: 12px + 12px;
        }

        &:after {
            left: 12px + 14px;
        }
    }

    &.dropup {
        margin-top: -5px;

        // NOTE: Note sure why these are special-cased.
        &:before {
            bottom: -12px;
            top: initial;
        }

        &:after {
            bottom: -12px;
            top: initial;
        }
    }
    .calendar {
        margin: 0;
        max-width: none;
        width: 315px;
    }
    .calendar-table {
        border: 0;
        border-radius: 0;
        padding: 0;
        position: relative;
    }

    table {
        border: 0;
        border-image-width: 0;
        margin: 0 0 8px 0;
        width: 100%;
        > thead {
            > tr {
                &:first-child {
                    > th {
                        height: 57px;
                        &:first-child, &:last-child {
                            width: 57px;
                        }
                    }
                }
                &:nth-child(2) {
                    th {
                        font-family: $font-family-book;
                        font-size: 14px;
                        padding: 10px 3px 2px;
                        text-transform: uppercase;
                    }
                }
            }

        }
    }
    th {
        border: 0;
        border-radius: 0;
        font-size: 14px;
        text-align: center;
        &.month {
            font-family: $font-family-book;
            font-size: 16px;
            min-width: 32px;
            padding-top: 4px;
            text-align: center;
            text-transform: uppercase;
            white-space: nowrap;
            width: auto;
        }
        &.available {
            &.prev, &.next {
                font-size: 30px;
                align-items: center;
                display: flex;
                justify-content: center;
            }
            &.next {
                //border-left: 1px solid rgba($white, 0.15);
                border-right: none;
                cursor: pointer;
                & i {
                    &.fa {
                        &:before {
                            /*content: '\f061';*/
                            content: '\f105';
                        }
                    }
                }
            }
            &.prev {
                //border-right: 1px solid rgba($white, 0.15);
                cursor: pointer;
                & i {
                    &.fa {
                        &:before {
                            /*content: '\f060';*/
                            content: '\f104';
                        }
                    }
                }
            }
        }
    }
    td {
        border-radius: 0;
        font-size: 14px;
        line-height: 34px;
        width: 40px;
        text-align: center;
        &.in-range {
            border-radius: 0;
        }
        &.start-date.end-date, &.start-date, &.end-date {
            border-radius: 0;
            font-family: $font-family-book;
        }
        &.off {
            &.start-date.end-date, &.start-date, &.end-date {
                font-family: $font-family-light;
            }
        }
        &.active {
            font-family: $font-family-book;
        }
        &.available {
            cursor: pointer;
            &:hover {
                border-radius: 0;
                font-family: $font-family-book;
            }
        }
    }
    .calendar-time {
        align-items: center;
        display: flex;
        flex-direction: row-reverse;
        height: 64px;
        margin: 0;
        padding: 0;
        text-align: left;
        width: 100%;
        > div {
            display: flex;
            flex: 1 1 auto;
            select {
                margin-left: 6px;
                margin-right: 6px;
            }
        }
    }
    .daterangepicker_input {
        @include input-container;
    }
    select {
        &.hourselect, &.minuteselect, &.secondsselect, &.ampmselect {
            font-family: $font-family-book;
            padding-left: 8px;
            padding-right: 34px;
            width: auto;
        }
        &::-ms-expand {
            display: none;
        }
    }
    .input-mini {
        border-radius: 0;
        flex: 1 1 auto;
        font-size: .875rem;
        height: calc(1.8125rem + 2px);
        line-height: 1.5;
        margin: 0;
        padding: .25rem .5rem;
        &.active {
            border-radius: 0;
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('date-picker-background-color', 'datetime');
        box-shadow: themed-component('date-picker-box-shadow', 'datetime');
        &:before, &:after {
            border-bottom-color: themed-component('date-picker-border-bottom-color', 'datetime');
        }
        &:before {
            border-bottom: themed-component('date-picker-before-border-bottom', 'datetime');
            border-left: themed-component('date-picker-before-border', 'datetime');
            border-right: themed-component('date-picker-before-border', 'datetime');
        }

        &:after {
            border-bottom: themed-component('date-picker-before-border', 'datetime');
            border-left: themed-component('date-picker-before-border', 'datetime');
            border-right: themed-component('date-picker-before-border', 'datetime');
        }
        &.dropup {
            &:before {
                border-bottom: themed-component('date-picker-dropup-before-border-bottom', 'datetime');
                border-top: themed-component('date-picker-before-border', 'datetime');
            }

            &:after {
                border-bottom: themed-component('date-picker-dropup-before-border-bottom', 'datetime');
                border-top: themed-component('date-picker-before-border', 'datetime');
            }
        }
        table {
            > thead {
                > tr {
                    &:first-child {
                        background-color: themed-component('date-picker-table-background-color', 'datetime');
                        > th {
                            background-color: themed-component('date-picker-table-background-color', 'datetime');
                        }
                    }
                    &:nth-child(2) {
                        th {
                            color: themed-component('date-picker-table-color', 'datetime');
                        }
                    }
                }
            }
        }
        th {
            &.month {
                color: themed-component('date-picker-table-th-color', 'datetime');
            }
            &.available {
                &.prev, &.next {
                    color: themed-component('date-picker-table-th-color', 'datetime');
                    &:hover {
                        background-color: themed-component('date-picker-table-available-background-color', 'datetime');
                        color: themed-component('date-picker-table-th-color', 'datetime');
                    }
                    &.next {
                        border-left: themed-component('date-picker-table-available-border', 'datetime');
                    }
                    &.prev {
                        border-right: themed-component('date-picker-table-available-border', 'datetime');
                    }
                }
            }
            //&.off {
            //    color: themed-component('date-picker-table-off-color', 'datetime');
            //    //&.in-range, &.start-date, &.end-date {
            //    //    background-color: themed-component('date-picker-table-off-background-color', 'datetime');
            //    //    color: themed-component('date-picker-table-off-color', 'datetime');
            //    //}
            //}
        }
        td {
            color: themed-component('date-picker-table-td-num-color', 'datetime');
            &.in-range {
                background-color: themed-component('date-picker-in-range-background-color', 'datetime');
                border-color: themed-component('date-picker-in-range-border-color', 'datetime');
                color: themed-component('date-picker-in-range-color', 'datetime');
            }
            &.start-date.end-date, &.start-date, &.end-date {
                background-color: themed-component('date-picker-table-td-active-background-color', 'datetime');
                color: themed-component('date-picker-table-td-color', 'datetime');
            }
            &.off {
                &.start-date.end-date, &.start-date, &.end-date {
                    background-color: themed-component('date-picker-inherit', 'datetime');
                    color: themed-component('date-picker-inherit', 'datetime');
                    &:hover {
                        background-color: themed-component('date-picker-table-td-available-background-color', 'datetime');
                        color: themed-component('date-picker-table-td-color', 'datetime');
                    }
                }
	            color: #6c757d;
            }

            &.available {
                &:hover {
                    background-color: themed-component('date-picker-table-td-available-background-color', 'datetime');
                    color: themed-component('date-picker-table-td-color', 'datetime');
                }
            }
        }
        .calendar-time {
            background-color: themed-component('date-picker-input-background-color', 'datetime');
        }
        .hourselect, .minuteselect, .secondsselect, .ampmselect {
            border-color: themed-component('date-picker-input-active-border-color', 'datetime');
            color: themed-component('date-picker-time-color', 'datetime');
            &:focus {
                border-color: themed-component('date-picker-input-active-border-color', 'datetime');
                box-shadow: themed-component('date-picker-input-box-shadow', 'datetime');
                color: themed-component('date-picker-time-color', 'datetime');
            }
        }

        .input-mini {
            &.active {
                border-color: themed-component('date-picker-input-active-border-color', 'datetime');
            }
        }
    }
    //- Theming ========================================================================================================
}

@media (min-width: 564px) {
    .datetime {
        .#{$prefix-class} {
            &.ltr {
                flex-direction: row;
                flex-wrap: wrap;
                width: 630px;
                .calendar {
                    float: left;
                    .daterangepicker_input {
                        @include input-container;
                        .input-mini {
                            margin: 16px 0;
                        }
                        .fa {
                            margin: 16px 0;
                        }
                        display: none;
                    }
                    &.left {
                        display: flex;
                        flex-direction: column-reverse;
                        .calendar-table {
                            padding-right: 0;
                            border-right: none;
                            border-top-right-radius: 0;
                            border-bottom-right-radius: 0;
                        }
                    }
                    &.right {
                        display: flex;
                        flex-direction: column-reverse;
                        .calendar-table {
                            padding-right: 0;
                        }
                    }
                }
                .left {
                    .daterangepicker_input {
                        padding: 0;
                    }
                }
                .ranges {
                    display: flex;
                    float: none;
                    height: 50px;
                    margin: 0;
                    width: 100%;
                    justify-content: flex-end;

                    .range_inputs {
                        display: flex;
                        flex-direction: row-reverse;
                        width: 315px;
                        .btn {
                            box-shadow: none;
                            text-transform: uppercase;
                            justify-content: center;
	                        border-radius: 0;
                        }
                        .applyBtn {
                            flex: 1;
                            width: 100%;
                        }
                    }
                }
            }
            &.single {
                width: 315px;
                &.ltr {
                    .calendar {
                        float: none;
                        .daterangepicker_input {
                            @include input-container;
                            .input-mini {
                                margin: 16px 0;
                            }
                            .fa {
                                margin: 16px 0;
                            }
                        }
                    }
                    .ranges {
                        display: flex;
                        float: none;
                        height: 50px;
                        margin: 0;
                        border-top: 0;
                        .range_inputs {
                            display: flex;
                            flex-direction: row-reverse;
                            width: 100%;
                            .btn {
                                box-shadow: none;
                                text-transform: uppercase;
                                justify-content: center;
	                            border-radius: 0;
                            }
                            .applyBtn {
                                flex: 1;
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
        //- Theming ========================================================================================================
        @include themify($themes) {
            .daterange {
                .#{$prefix-class} {
                    &.ltr {
                        .ranges {
                            border-top: themed-component('date-picker-input-border', 'datetime');
                        }
                    }
                }
            }
            .#{$prefix-class} {
                .calendar-time {
                    border-bottom: themed-component('date-picker-input-border', 'datetime');
                }
                &.ltr {
                    .ranges {
                        background-color: themed-component('date-picker-range-background-color', 'datetime');
                    }
                    .range_inputs {
                        border-left: themed-component('date-picker-input-border', 'datetime');
                    }
                    .calendar {
                        &.right {
                            .daterangepicker_input {
                                border-left: themed-component('daterange-picker-input-border-left', 'datetime');
                            }
                        }
                        &.left {
                            .daterangepicker_input {
                                .fa {
                                    color: themed-component('date-picker-calendar-time-icon-color', 'datetime');
                                }
                            }
                        }
                    }
                }
            }
        }
        //- Theming ========================================================================================================
    }
