@import '../variables';
@import '../mixins/mixins';
@import '../themes';
@import '../functions';

.progress {
    border-radius: 0;
    line-height: 1rem;

    //- Theming ========================================================================================================
    @include themify($themes) {
        background-color: themed-component('background-color', 'progress-bars');
        .progress-bar {
            font-weight: 900;
            @each $name, $color in $all-colors {
                &.bg-#{$name} {
                    $background-color: themed-component(#{$name + '-background-color'}, 'colors');

                    color: test-contrast($background-color);

                    @if $name == 'warning' { color: $cl-blue-dark; }
                }
            }
            &.bg-default {
                background-color: themed-component('primary-background-color', 'colors') !important;
                //color: themed-component('white-color', 'colors');
                color: themed-component('white-color', 'colors');
            }
        }
    }
    //- Theming ========================================================================================================
}