@import '../variables';
@import '../mixins/mixins';
@import '../themes';


.toast {
    max-width: 348px;
    box-shadow: 0 0 8px 0 rgba($black, 0.35);
    border-radius: 0;

    .toast-header {
        padding: 8px 14px;
        font-size: 16px;
        .close {
            display: flex;
            justify-content: center;
            align-items: center;
            border-left: 1px solid $alto;
            height: 42px;
            width: 42px;
            margin: -8px -14px -8px 20px;
            text-shadow: none;
            > span {
                font-size: 38px;
                font-weight: normal;
            }
            &:hover {
                background-color: rgba($black, 0.01);
            }
            &:focus, &:active {
                @include set-focus;
            }
        }
    }

    .toast-body {
        padding: 8px 14px;
        font-size: 14px;
    }

    $merged: map-merge($all-colors, $theme-colors);
    @each $name, $color in $merged {
        &.toast-#{$name} {
            //- Theming ========================================================================================================
            @include themify($themes) {
                $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                $border-color: themed-component(#{$name + '-border-color'}, 'colors');
                $text-color: test-contrast($background-color);
                $hover-background-color: darken($background-color, 6%);

                @if ($name == 'default') or ($name == 'light') or ($name == 'secondary') {
                    $background-color: $seashell;
                    $border-color: transparent;
                    $hover-background-color: darken($background-color, 6%);
                }

                color: $text-color;
                background-color: $background-color;
                border-color: rgba($black, 0.1);

                .toast-header {
                    color: $text-color;
                    background-color: $background-color;
                    border-color: rgba($black, 0.1);
                    .close {
                        color: $text-color;
                        border-color: rgba($black, 0.1);
                        &:hover {
                            background-color: $hover-background-color;
                        }
                    }
                }
            }
        }
    }
}






