@import '../variables';
@import '../themes';

$timelineItemPX: 12px;
$badgeSize: 60px;
$badgeBorderSize: 8px;

.timeline {
    &:not(.mini) {
        .timeline-item {
            display: flex;
            flex-direction: row;
            padding: 20px $timelineItemPX;
            position: relative;
            &:first-child {
                padding-top: 0;
            }
            &:before {
                bottom: 0;
                content: '';
                height: 100%;
                left: ($badgeSize / 2) + $timelineItemPX - ($badgeBorderSize / 2);
                position: absolute;
                top: 0;
                width: $badgeBorderSize;
                z-index: -1;
            }
            &:last-child {
                &:before {
                    bottom: auto;
                    height: 20%;
                }
            }
            .time-alert {
                font-size: 18px;
                left: -94px;
                margin-top: 0;
                position: absolute;
            }
            .time-badge {
                align-items: center;
                border-radius: 50%;
                display: flex;
                flex: 0 0 auto;
                height: $badgeSize;
                justify-content: center;
                width: $badgeSize;
                .badge-icon {
                    font-size: 28px;
                }
            }
            .time-panel {
                margin-left: 20px;
                padding: 20px;
                position: relative;
                width: 100%;
                &:before {
                    border-style: solid;
                    border-width: 12px 12px 12px 0;
                    content: '';
                    height: 0;
                    left: -12px;
                    position: absolute;
                    width: 0;
                }
                > .panel-roof {
                    display: flex;
                    justify-content: space-between;
                    .time {
                        font-family: $font-family-book;
                    }
                }
                h3 {
                    font-size: 12px;
                }

            }
        }
    }

    &.mini {
        .timeline-item {
            display: flex;
            flex-direction: row;
            justify-content: center;
            padding: 5px $timelineItemPX;
            position: relative;

            &:first-child {
                padding-top: 0;
            }
            &:last-child {
                &:before {
                    bottom: auto;
                    height: 20%;
                }
            }
            .time-alert {
                align-items: center;
                font-size: 22px;
                left: -35px;
                margin-top: 0;
                position: absolute;
                top: 50%;
                transform: translate(-50%, -50%);
                .fa {
                    font-size: 24px;
                }
            }
            .time-badge {
                align-items: center;
                display: flex;
                flex: 0 0 auto;
                justify-content: center;
                width: 23px;
                .badge-icon {
                    align-items: center;
                    display: flex;
                    font-size: 13px;
                }
            }
            .time-panel {
                padding: 10px 10px 11px;
                position: relative;
                width: 100%;
                p {
                    font-size: 12px;
                    line-height: 1.5;
                }
                > .panel-roof {
                    display: flex;
                    justify-content: space-between;
                    .time {
                        font-family: $font-family-book;
                    }
                }
                h2 {
                    font-size: 14px;
                }
                h3 {
                    font-size: 12px;
                    line-height: 1.5;
                }

            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        &:not(.mini) {
            .timeline-item {
                &:before {
                    background-color: themed-component('panel-background-color', 'timeline');
                }
                .time-alert {
                    color: themed-component('time-alert-color', 'timeline');
                }
                .time-badge {
                    background-color: themed-component('time-badge-background-color', 'timeline');
                    border: themed-component('time-badge-border', 'timeline');
                    .badge-icon {
                        color: themed-component('time-badge-icon-color', 'timeline');
                    }
                }
                .time-panel {
                    background-color: themed-component('panel-background-color', 'timeline');
	                color: themed-component('panel-color', 'timeline');
	                border-radius: themed-component('panel-border-radius', 'timeline');
                    &:before {
                        border-color: themed-component('panel-border-color', 'timeline');
                    }
                }
                $merged: map-merge($all-colors, $theme-colors);
	            $merged-colors: map-merge($merged, $new-brand-colors19);
                @each $name, $color in $merged-colors {
                    &.timeline-item-#{$name} {
                        .time-badge {
                            .badge-icon {
                                color: themed-component(#{$name + '-color'}, 'colors');
                            }
                        }
                    }
                }
            }
        }
        &.mini {
            .timeline-item {
                .time-alert {
                    color: themed-component('time-alert-color', 'timeline');
                }
                .time-panel {
                    background-color: themed-component('panel-background-color', 'timeline');
	                color: themed-component('panel-color', 'timeline');
	                border-top-right-radius: themed-component('panel-border-radius', 'timeline');
	                border-bottom-right-radius: themed-component('panel-border-radius', 'timeline');
                }

                $merged: map-merge($all-colors, $theme-colors);
	            $merged-colors: map-merge($merged, $new-brand-colors19);
                @each $name, $color in $merged-colors {
                    &.timeline-item-#{$name} {
                        .time-badge {
                            $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                            background-color: $background-color;
	                        border-top-left-radius: themed-component('panel-border-radius', 'timeline');
	                        border-bottom-left-radius: themed-component('panel-border-radius', 'timeline');

                            .badge-icon {
                                color: test-contrast($background-color);
                                @if index('orange' 'orange-dark' 'warning', $name) { color: $cl-blue-dark; }
                            }
                        }
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}
