@import '../variables';
@import '../themes';

.contact-card {
    position: relative;
    width: 290px;

    .card {
        .card-header {
            padding: 16px 22px;
            border-width: 1px;
            border-style: solid;

            .person-name {
                font-size: 22px;
                line-height: 24px;
            }

            .person-title {
                font-size: 16px;
                line-height: 18px;
            }

            &:after {
                content: '';
                font-family: FontAwesome;
                position: absolute;
                right: 16px;
                font-size: 2rem;
            }
        }

        .card-body {
            font-size: 14px;
            line-height: 21px;
            padding: 16px 22px;
        }

        .contact-icon {
            font-size: 1.3rem;
            line-height: 1.5rem;
            display: inline-block;
            margin-left: 20px;
            &:first-child {
                margin-left: 0;
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .card {
            border-color: themed-component('card-border-color', 'contact-card');

            .card-header {
                background: themed-component('card-header-background-color', 'contact-card');
                //border-bottom-color: themed-component('card-border-color', 'contact-card');
                //border-color: themed-component('card-border-color', 'contact-card');
	            border-color: transparent;
	            color: themed-component('card-header-color', 'contact-card');

	            h2, .person-name {
		            color: themed-component('card-header-color', 'contact-card');
	            }

                &:after {
	                color: themed-component('card-header-color', 'contact-card');
                }
            }
            .card-body {
                background-color: themed-component('card-body-background-color', 'contact-card');
	            color: themed-component('card-body-color', 'contact-card');
            }
            .card-footer {
                background-color: themed-component('card-footer-color', 'contact-card');
                border-top-color: themed-component('card-border-color', 'contact-card');
            }
            .contact-icon {
                color: themed-component('card-contact-icon-color', 'contact-card');
            }
        }
    }
    //- Theming ========================================================================================================
}

.popover {
    &.contact-card-popover {
        min-width: 290px;

        .popover-header {
            display: none;
        }
        .popover-body {
            padding: 0;
            .card {
                border: 0;
                border-radius: 4px;
                .card-header {
                    min-height: 0;
                    padding: 10px 22px;
                    &:after {
                        content: '';
                        font-family: FontAwesome;
                        position: absolute;
                        right: auto;
                        left: 16px;
                        font-size: calc-rem(22px);
                    }
                }
                .card-body {
                    .person-name {
                        font-size: 22px;
                        line-height: 24px;
                    }
                    .person-title {
                        font-size: 16px;
                        line-height: 18px;
                    }
                    font-size: 14px;
                    padding: 16px;
                }
                .contact-icon {
                    font-size: 1.3rem;
                    line-height: 1.5rem;
                    display: inline-block;
                    margin-left: 20px;
                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }



