    //- Theming ========================================================================================================
    @include themify($themes) {
        &.contact-card-popover {
            border-color: themed-component('card-border-color', 'contact-card');
            &.bs-popover-left {
                .arrow:before {
                    border-left-color: themed-component('card-popover-arrow-border-color', 'contact-card');
                }
                .arrow:after {
                    border-left-color: themed-component('card-body-background-color', 'contact-card');
                }
            }
            &.bs-popover-right {
                .arrow:before {
                    border-right-color: themed-component('card-popover-arrow-border-color', 'contact-card');
                }
                .arrow:after {
                    border-right-color: themed-component('card-body-background-color', 'contact-card');
                }
            }
            &.bs-popover-top {
                .arrow:before {
                    border-top-color: themed-component('card-popover-arrow-border-color', 'contact-card');
                }
                .arrow:after {
                    border-top-color: themed-component('card-body-background-color', 'contact-card');
                }
            }
            &.bs-popover-bottom {
                .arrow:before {
                    border-bottom-color: themed-component('card-popover-arrow-border-color', 'contact-card');
                }
                .arrow:after {
                    border-bottom-color: themed-component('card-popover-arrow-border-bottom-color', 'contact-card');
                }
            }
            .popover-body {
                .card {
                    .card-header {
                        background: themed-component('card-popover-header-background-color', 'contact-card');
                        border-bottom-color: themed-component('card-border-color', 'contact-card');

                        &:after {
                            color: themed-component('card-popover-header-color', 'contact-card');
                        }
                    }
                    .card-body {
                        background-color: themed-component('card-body-background-color', 'contact-card');
	                    color: themed-component('card-body-color', 'contact-card');

	                    h2, .person-name {
		                    color: themed-component('card-body-color', 'contact-card');
	                    }
                    }
                    .card-footer {
                        background-color: themed-component('card-popover-footer-background-color', 'contact-card');
                        border-top-color: themed-component('card-border-color', 'contact-card');
                    }
                    .contact-icon {
                        color: themed-component('card-popover-icon-color', 'contact-card');
                    }
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

@media screen and (max-width: map-get($grid-breakpoints, 'sm')) {
    .contact-card {
        width: 100%;
    }
}



