@import '../variables';
@import '../mixins/mixins';
@import '../themes';


.popover {
    border-radius: 0;
    max-width: 330px;
    z-index: 1010;
    .popover-header {
        border-radius: 0;
        font-family: $font-family-book;
        font-size: calc-rem(16px);
        line-height: calc-rem(18px);
        padding: 18px 25px 13px;
        text-transform: unset;
        &:before {
            border: 0;
            border-bottom-color: transparent;
            display: none;
        }
    }
    .popover-body {
        font-family: $font-family-light;
        font-size: calc-rem(16px);
        padding: 18px 25px 20px;

        ul {
            padding-left: 0 !important;
        }
    }
    &.popover-lg {
        min-width: 540px;
    }
    &.scrolling {
        .popover-body {
            max-height: 180px;
            overflow-y: auto;
        }
    }

    @include themify($themes) {
        $background-color: themed-component('background-color', 'popovers');
        $border: themed-component('border-color', 'popovers');
        $box-shadow: themed-component('box-shadow', 'popovers');
        $color: themed-component('color', 'popovers');

        background-color: $background-color;
        border: 1px solid $border;
        box-shadow: $box-shadow;
        color: $color;

        .popover-header {
            background-color: themed-component('header-background-color', 'popovers');
            border-bottom-color: themed-component('header-bottom-border-color', 'popovers');
        }
        .popover-body {
            color: $color;
        }
    }
}

@include themify($themes, true) {
    $background-color: themed-component('background-color', 'popovers');
    $border-color: themed-component('border-color', 'popovers');
    $header-background-color: themed-component('header-background-color', 'popovers');
    @include arrow-color_top($background-color, $border-color);
    @include arrow-color_right($background-color, $border-color);
    @include arrow-color_bottom($header-background-color, $border-color);
    @include arrow-color_left($background-color, $border-color);
}

.popover-notify {
    max-width: 400px;
    width: 400px;
    border-radius: 0;
    border: 0;
    z-index: 1021;
    .arrow::after {
        margin-left: -4px;
    }
    .arrow::before {
        margin-left: -4px;
    }
    .popover-header {
        border: 0;
        border-radius: 0;
        text-transform: uppercase;
        font-size: 16px;
        line-height: 18px;
        height: 56px;
        padding-top: 2px;
        padding-bottom: 0;
        display: flex;
        align-items: center;
    }
    .popover-body {
        padding: 0;
        height: 320px;
        overflow-y: auto;
        .list-group {
            padding-left: 0;
            .list-group-item {
                display: flex;
                padding: 0;
                &:hover {
                    //background-color: #f7f9f8;
                }
            }
        }
        .notifier {
            width: 32px;
            position: relative;
            .unread {
                &:after {
                    background-color: $gray;
                    border-radius: 50%;
                    content: '';
                    height: 9px;
                    position: absolute;
                    left: 50%;
                    margin-left: -5px;
                    top: 16px;
                    width: 9px;
                }
            }
        }
        .notifier-message {
            padding-top: 12px;
            padding-bottom: 12px;
            position: relative;
            flex: 1 1 auto;
            .title {
                line-height: 18px;
                font-size: 16px;
                font-family: $font-family-book;
            }
            .desc {
                font-size: 12px;
                line-height: 18px;
                color: $gray;
                width: 336px;
            }
            .status {
                font-size: 12px;
                line-height: 18px;
            }
            .time {
                font-size: 11px;
                font-family: $font-family-book;
                padding-right: 16px;
                white-space: nowrap;
            }
        }
    }

    $merged-colors: map-merge($all-colors, $theme-colors);
    $merged-colors: map-merge($merged-colors, $new-brand-colors19);

    @each $name, $color in $merged-colors {
        &.notify-#{$name} {
            @include themify($themes) {
                $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                $color: test-contrast($background-color);
                $item-hover-background-color: transparentize($background-color, 0.92);
                $item-hover-color: test-contrast($item-hover-background-color);

                .popover-header {
                    @if index('orange' 'orange-dark' 'warning' 'amethyst',$name) { $color: $cl-blue-dark; }

                    background-color: $background-color;
                    color: $color;
                }
                .arrow::after {
                    border-bottom-color: $background-color;
                }
                .arrow::before {
                    border-bottom-color: $background-color;
                }
                .popover-body {
                    .notifier {
                        .unread {
                            &:after {
                                background-color: $background-color;
                            }
                        }
                    }
                }
                .list-group-item {
                    background-color: transparent;
                    border-color: transparentize(themed-component('border-color', 'popovers'), 0.8);
                    &:hover {
                        background-color: $item-hover-background-color;
                    }
                    &.active {
                        color: $color;
                        background-color: rgba($background-color, 0.08);
                        border-color: transparent;
                    }
                }
            }
        }
    }
}