@import '../variables';
@import '../mixins/mixins';

.custom-radio {
    //align-items: center;
    cursor: pointer;
    display: flex;
    margin-bottom: -3.5px;
    padding: 5px 16px 5px 0;
    vertical-align: text-top;

    .custom-control-input {
        & ~ .custom-control-indicator {
            border-radius: 50%;
            display: inline-block;
            height: 16px;
            min-height: 16px;
            margin-right: 6px;
            opacity: 1;
            pointer-events: auto;
            position: relative;
            width: 16px;
            min-width: 16px;
            transition: box-shadow ease-in-out 0.15s;

            &:after {
                border-radius: 50%;
                content: '';
                display: inline-block;
                height: 10px;
                left: 2px;
                position: absolute;
                top: 2px;
                transition: all 0.2s;
                width: 10px;
            }

            &:disabled {
                box-shadow: none;
            }

            &:focus {
                @include set-focus;
            }
            & ~ .custom-control-description {
                line-height: 18px;
                margin-top: -1px;
                flex: 1 1 auto;
            }
        }

        &:checked {
            & ~ .custom-control-indicator {

                &:after {
                    opacity: 1;
                    transform: scale(1);
                }

                &:disabled {
                    box-shadow: none;

                    & ~ .custom-control-description {
                        cursor: not-allowed;
                        opacity: 0.7;
                    }
                }
            }
        }

        &:not(:checked) {
            & ~ .custom-control-indicator {
                &:after {
                    opacity: 0;
                    transform: scale(0);
                }
            }
        }

        &:disabled {
            & ~ .custom-control-indicator {
                box-shadow: none;
                cursor: not-allowed;
                opacity: 0.7;
            }

            & ~ .custom-control-description {
                cursor: not-allowed;
                opacity: 0.7;
            }
        }
        &:focus {
            & ~ .custom-control-indicator {
                @include set-focus;
            }
        }
    }
    &.custom-control-inline {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    @include themify($themes) {
        .custom-control-input {
            & ~ .custom-control-indicator {
                background-color: themed-component('radio-background-color', 'forms');
                border: 1px solid themed-component('radio-border-color', 'forms');

                &:after {
                    background-color: themed-component('radio-border-color', 'forms');
                }
            }
	        & ~ .custom-control-description {
		        color: themed-component('radio-color', 'forms');
	        }

            &:checked {
                & ~ .custom-control-indicator {
                    background: themed-component('radio-background-color', 'forms') none;
                    border: 1px solid themed-component('radio-border-color', 'forms');

                    &:disabled {
                        background-color: themed-component('radio-background-color', 'forms');
                    }
                }
            }
        }
    }
}

//Apply alternate colors to an active radio
@each $name, $color in $action-colors {
    @include themify($themes, true) {
        $background-color: themed-component(#{$name + '-background-color'}, 'colors');
        $border-color: themed-component(#{$name + '-border-color'}, 'colors');
        $color: themed-component(#{$name + '-color'}, 'colors');

        @if $name == 'default' {
	        $border-color: themed-component('gray-border-color', 'colors');
	        $background-color: themed-component('gray-border-color', 'colors');
        }

        .custom-radio {
            .custom-control-input {
                &:checked.radio-#{$name} {
                    & ~ .custom-control-indicator {
                        border-color: $border-color;

                        &:after {
                            background-color: $background-color;
                        }
                    }
                }
            }
        }
    }
}

fieldset {
    margin-bottom: 0.5rem;
    [role='radiogroup'] {
        & ~ .invalid-feedback {
            display: block;
        }
    }
}
