@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.jumbotron {
    border-radius: 0;
}

.server-alert {
    .jumbotron {
        margin-bottom: 8px;
        padding: 4rem;
    }

    @for $i from 1 through 4 {
        .display-#{$i} {
            padding-bottom: 16px;
        }
    }

    .server-alert-footer {
        align-items: center;
        display: flex;
        flex-direction: column;
        .clgx-logo {

            height: 80px;
            width: 90px;
        }
        .small {
            margin-top: -16px;
        }
    }

    @include themify($themes) {
        $background-color: themed-component('server-alert-background-color', 'alerts');
        $color: themed-component('server-alert-color', 'alerts');
        $display-color: themed-component('server-alert-display-color', 'alerts');
        $lead-color: themed-component('server-alert-lead-color', 'alerts');

        .jumbotron {
            background-color: $background-color;
            color: $color;
        }
        @for $i from 1 through 4 {
            .display-#{$i} {
                color: $color;
            }
        }
        .lead {
            color: $lead-color;
        }
        .server-alert-footer {
            .clgx-logo {
                $use-color-logo: themed-component('server-alert-color-logo', 'alerts');

                @if ($use-color-logo == true) {
                    @include clgx-logo-color;
                } @else {
                    @include clgx-logo-white;
                }
            }
        }
    }
}
