@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.cl-pagination {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-family: $font-family-book;
    font-size: calc-rem(14px);
    height: auto;
    margin-top: calc-rem(-17px);
    min-width: 374px;
    width: 100%;

    .cl-page-count {
        align-items: center;
        align-self: stretch;
        display: flex;
        flex: 1 1;
        font-size: calc-rem(14px);
        min-height: 50px;
        padding-left: 14px;
        padding-right: 14px;
        vertical-align: middle;
        white-space: nowrap;
        .cl-page-select {
            display: inline-block;
            margin-right: 14px;
            width: auto;
        }
    }
    .pagination {
        border-radius: 0;
        display: flex;
        flex: 1 1;
        margin: 0 0 0 auto;
        min-height: 50px;
        .page-item {
            align-items: center;
            display: flex;
            flex: 1 1;
            justify-content: center;
            margin: 0;
            min-width: 50px;
            &:active, &:focus {
                @include set-focus;
            }
            .page-link {
                align-self: center;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                line-height: 49px;
                padding: 0;
                text-align: center;
                text-decoration: none;
                width: 100%;
                &:focus {
                    @include set-focus;
                }
            }
            &.active {
                .page-link {
                    &:after {
                        border-radius: 30px;
                        content: '';
                        height: 30px;
                        left: 50%;
                        margin-left: -15px;
                        margin-top: -15px;
                        position: absolute;
                        top: 50%;
                        width: 30px;
                        z-index: 999;
                    }
                }
            }
            &.previous {
                border-left: 1px solid $alto;
                margin-left: -1px;
                min-width: auto;
                .page-link {
                    padding-left: 16px;
                    padding-right: 16px;
                }
            }
            &.next {
                min-width: auto;
                .page-link {
                    padding-left: 16px;
                    padding-right: 16px;
                }
            }
        }
    }

    @media screen and ( max-width: 768px ) {
        .cl-page-count {
            font-size: calc-rem(14px);
            padding-left: 8px;
            padding-right: 8px;
            .cl-page-select {
                margin-right: 14px;
            }
        }
        .pagination {
            .page-item {
                min-width: 28px;
                padding-left: 2px;
                padding-right: 2px;
                &.previous {
                    .page-link {
                        padding-left: 8px;
                        padding-right: 8px;
                    }
                }
                &.next {
                    .page-link {
                        padding-left: 8px;
                        padding-right: 8px;
                    }
                }
            }
        }
    }
}

@include themify($themes, true) {
    .cl-pagination {
        background-color: themed-component('background-color', 'pagination');
        border-left: 1px solid themed-component('border-color', 'pagination');
        border-right: 1px solid themed-component('border-color', 'pagination');
        border-top: 1px solid themed-component('border-color', 'pagination');

        .cl-page-count {
            background-color: themed-component('background-color', 'pagination');
            border-bottom: 1px solid themed-component('border-color', 'pagination');
        }

        .pagination {
            background-color: themed-component('background-color', 'pagination');
            border-bottom: 1px solid themed-component('border-color', 'pagination');

            .page-item {
                border-left: 1px solid themed-component('border-color', 'pagination');

                &:active, &:focus {
                    background-color: themed-component('item-active-hover-background-color', 'pagination');
                    border-color: themed-component('item-active-hover-border-colo', 'pagination');
                    color: themed-component('item-active-hover-color', 'pagination');
                }

                &:hover {
                    background-color: themed-component('item-active-hover-background-color', 'pagination');
                    border-color: themed-component('item-active-hover-border-color', 'pagination');
                    color: themed-component('item-active-hover-color', 'pagination');
                }

                .page-link {
                    color: themed-component('color', 'pagination');
                    &:focus {
                        background-color: themed-component('item-active-hover-background-color', 'pagination');
                        border-color: themed-component('item-active-hover-border-color', 'pagination');
                        color: themed-component('item-active-hover-color', 'pagination');
                    }
                }

                &.active {
                    .page-link {
                        color: themed-component('selected-color', 'pagination');
                        &:after {
                            border: 3px solid themed-component('selected-circle-color', 'pagination');
                        }
                    }
                }

                &.previous {
                    border-left: 1px solid themed-component('border-color', 'pagination');
                }

                &.next {
                    border-left: 1px solid themed-component('border-color', 'pagination');
                }
            }
        }
    }
}