@import 'variables';
@import '../mixins/mixins';

body {
    @each $brand, $map in $brand-colors {
        &.brand-#{$brand} {
            $color: map-get($map, 'color');

            a {
                color: $color;
            }

            .cl-main-nav {
                border-bottom: 1px solid $color;
                .navbar-nav, .nav {
                    .nav-divider {
                        background-color: $color;
                    }
                }
            }

            .layout-drawer {
                .title-command-area {
                    background-color: $color;
                }
            }

            .sidebar-nav {
                > .nav {
                    .nav-link {
                        &.active {
                            background-color: $color;

                            &:hover {
                                color: $white;
                            }
                        }

                        &:hover {
                            color: $color;
                        }
                    }

                    > .sidebar-nav-subnav {
                        > .nav-link-collapse {
                            &:before {
                                color: $color;
                            }
                        }
                    }
                }
            }

            .tertiary-nav {
                .nav {
                    .nav-link {
                        &.active {
                            color: $color;
                        }
                    }
                }

            }

            .page-command-area {
                $background-color: $color;
                $gradient-start-color: map-get($map, 'gradient-start-color');
                $gradient-end-color: map-get($map, 'gradient-end-color');

                background-color: $background-color;
                background-image: linear-gradient(45deg, $gradient-start-color 5%, $gradient-end-color 100%);
	            // background-image: $pca-geo-svg, linear-gradient(45deg, $gradient-start-color 5%, $gradient-end-color 100%);
            }

            .footer {
                &.footer-dark {
                    .footer-nav {
                        .nav-divider {
                            background-color: $color;
                        }
                    }
                }
            }

        }
    }
}
