@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.modal-full {
    max-width: 1440px;
    width: 100%;

    @media screen and (max-width: 1480px) {
        width: 100%;
        margin: 0;
        padding: 1.75rem;
    }
}

.modal-lg {
    max-width: 800px;
    width: 800px;

    @media screen and (max-width: 840px) {
        width: 100%;
        margin: 0;
        padding: 1.75rem;
    }
}

.modal {
    button.close, .close {
        align-items: center;
        display: flex;
        float: none;
        height: 63px;
        padding: 0 20px;
        transition: box-shadow ease-in-out 0.15s;
        &:focus, span:focus {
            @include set-focus;
        }
    }
    .modal-dialog {

        .modal-content {
            border: 0;
            border-radius: 0;
            font-family: $font-family-light;

            .modal-nav {
                display: flex;
                height: 56px;

                .nav {
                    flex-wrap: nowrap;
                }
                .nav-item {
                    display: flex;

                    &:first-child {
                        border-left: 0;
                    }
                    .close {
                        align-items: center;
                        display: flex;
                        float: none;
                        height: 54px;
                        padding: 0 20px;
                    }
                }
                .nav-link {
                    align-items: center;
                    display: flex;
                    font-family: $font-family-book;
                    font-size: 14px;
                    line-height: 16px;
                    padding: 0 28px;
                    position: relative;
                    text-decoration: none;

                    @media (max-width: map-get($grid-breakpoints, 'lg')) {
                        font-size: 14px;
                        padding: 0 10px;
                    }

                    &.active {
                        &:after {
                            border-style: solid;
                            border-width: 11px 11px 0;
                            bottom: -9px;
                            content: ' ';
                            height: 11px;
                            justify-self: center;
                            left: 0;
                            margin: auto;
                            position: absolute;
                            right: 0;
                            width: 11px;
                            z-index: 1000;
                        }
                    }
                }
                .nav {
                    & + .nav {
                        .nav-item {
                            &:last-child {
                                border-right: 0;
                            }
                        }
                    }
                }
            }
            .modal-header {
                ~ .modal-nav {
                    height: 45px;
                    .nav-item {
                        .close {
                            height: 43px;
                        }
                    }
                }
                align-items: center;
                border-radius: 0;
                font-family: $font-family-light;
                min-height: 64px;
                padding: 0 calc-rem(16px) 0 calc-rem(28px);

                .modal-title {
                    font-family: $font-family-book;
                    font-size: calc-rem(22px);
                    font-weight: normal;
                    text-transform: none;
                }
            }
            .modal-body {
                padding: calc-rem(28px) calc-rem(28px);

                .separator {
                    margin-left: calc-rem(-28px);
                    margin-right: calc-rem(-28px);
                }
            }
            .modal-footer {
                border-radius: 0;
                padding: calc-rem(16px) calc-rem(20px);
            }
        }

        &.modal-sm {
            button.close, .close {
                height: 50px;
                margin: -1rem 0 -1rem auto;
            }
            .modal-content {
                .modal-header {
                    min-height: 48px;
                    padding: 10px 0 10px 16px;
                    .modal-title {
                        font-size: 1.175rem;
                    }
                }
                .modal-nav {
                    height: 48px;
                    .nav-item {
                        // padding: 0 10px;
                        .close {
                            height: 46px;
                            padding: 0 10px;
                            margin: 0;
                        }
                    }
                    .nav-link {
                        padding: 0 15px;
                    }
                }
                .modal-footer {
                    padding: 10px;
                }
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        button.close, .close {
            border-left: themed-component('modal-border', 'modals');
            color: themed-component('modal-color', 'modals');
            &:focus, span:focus {
                background-color: themed-component('modal-background-color-focus', 'modals');
                border-color: themed-component('modal-border-focus', 'modals');
                color: themed-component('modal-active-color', 'modals');
            }
            &:hover {
                border-color: themed-component('modal-border-focus', 'modals');
                color: themed-component('modal-active-color', 'modals');
            }
        }
        .modal-dialog {
            .modal-content {
                box-shadow: themed-component('modal-box-shadow', 'modals');
                .modal-nav {
                    background: themed-component('modal-background-color', 'modals');
                    border-bottom: themed-component('modal-nav-border', 'modals');
                    .nav-item {
                        border-left: themed-component('modal-border', 'modals');
                        &:last-child {
                            border-right: themed-component('modal-border', 'modals');
                        }
                        .close {
                            border-left: themed-component('modal-border', 'modals');
                            color: themed-component('modal-color', 'modals');
                        }
                    }
                    .nav-link {
                        color: themed-component('modal-color', 'modals');
                        &:hover {
                            color: themed-component('modal-nav-link-hover-color', 'modals');
                        }
                        &.disabled {
                            color: themed-component('modal-nav-link-disabled-color', 'modals');
                        }
                        &.active {
                            background-color: themed-component('modal-nav-active-background-color', 'modals');
                            color: themed-component('modal-nav-active-color', 'modals');
                            text-shadow: themed-component('modal-active-text-shadow', 'modals');
                            &:after {
                                border-color: themed-component('modal-active-after-border-color', 'modals')
                            }
                        }
                    }
                }
                .modal-header {
                    background: themed-component('modal-background-color', 'modals');
                    border-bottom: themed-component('modal-border', 'modals');
                    .modal-title {
                        color: themed-component('modal-color', 'modals');
                    }
                }
                .modal-body {
                    background: themed-component('modal-body-background-color', 'modals');
                    .separator {
                        border-top: themed-component('modal-border', 'modals');
                    }
                }
                .modal-footer {
                    background-color: themed-component('modal-background-color', 'modals');
                    border-top: themed-component('modal-border', 'modals');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}
