@import '../variables';
@import '../mixins/mixins';

@include form-validation-state('valid', $form-feedback-valid-color);
@include form-validation-state('invalid', $form-feedback-invalid-color);

fieldset {
    legend {
        @include form-labels;
    }
}

.form-group {
    label {
        @include form-labels;
    }
    &.has-feedback {
        .invalid-feedback {
            display: block;
        }
    }
}

.required {
    &:after {
        content: ' *';
    }
}

.form-control {
    // border-radius: 0;
    &.is-invalid {
        & ~ .invalid-feedback {
            display: block;
        }
    }
    &:disabled {
        cursor: not-allowed;
    }
    &.no-border {
        border: 0;
    }
}

.custom-file {
    &.is-invalid {
        & ~ .invalid-feedback {
            display: block;
        }
    }
    &.disabled {
        opacity: 0.7;
    }
    .custom-file-input:disabled {
        cursor: not-allowed;
    }
}

.input-group {
    height: 38px;
    width: 100%;

    .form-control {
        // border-radius: 0;
    }
    &.left-icon {
        .input-icon {
            align-items: center;
            bottom: 0;
            display: flex;
            justify-content: center;
            position: absolute;
            top: -1px;
            width: 38px;
            z-index: 4;
        }
        .form-control {
            padding-left: 38px;
        }
    }
    &.right-icon {
        .input-icon {
            align-items: center;
            bottom: 0;
            display: flex;
            justify-content: center;
            position: absolute;
            right: 0;
            top: -1px;
            width: 38px;
            z-index: 4;
        }
        .form-control {
            padding-right: 38px;
        }
    }
    .input-group-addon {
        &.input-icon {
            background-color: transparent;
            border-color: transparent;
        }
    }
    .input-group-btn {
        display: flex;
        margin-left: -1px;
        margin-right: -1px;
        z-index: 5;
	    border-top-right-radius: 0;
	    border-bottom-right-radius: 0;

        .btn {
            align-items: center;
            display: flex;
            float: right;
	        border-top-left-radius: 0;
	        border-bottom-left-radius: 0;
        }
    }
    &.input-group-lg {
        height: 46px;
    }
    &.input-group-sm {
        height: 28px;
    }
}

.invalid-feedback {
    display: none;
    font-size: $font-size-base;
    line-height: 2;
    &:before {
        @extend .fa;
        @extend .fa-warning;
        padding-right: calc-rem(6px);
    }
}

// Position radios and checkboxes better
input {
    &[type='radio'],
    &[type='checkbox'] {
        line-height: normal;
        margin: 4px 0 0;
    }
}

@include themify($themes, true) {
    .form-group {
        label {
            color: themed-component('label-color', 'forms');
        }
    }
    .required {
        color: themed-component('required-color', 'forms') !important;
        &:after {
            color: themed-component('required-color', 'forms');
        }
    }
    .form-control {
        background-color: themed-component('input-background-color', 'forms');
        border-color: themed-component('input-border-color', 'forms');
        color: themed-component('input-color', 'forms');
        &::placeholder {
            color: themed-component('placeholder-color', 'forms');
        }
        &:not(.is-invalid) {
            border: 1px solid themed-component('input-border-color', 'forms');
            &:focus {
                @include set-focus(themed-component('focus-color', 'forms'));
            }
        }
        &:disabled {
            background-color: themed-component('disabled-background-color', 'forms');
            opacity: 0.65;
        }
    }
    .custom-file {
        &.disabled {
            background-color: themed-component('disabled-background-color', 'forms');
        }
    }
    .input-group {
        .input-group-prepend, .input-group-append {
            .input-group-text {
                background-color: themed-component('input-group-addon-background-color', 'forms');
	            border: themed-component('input-group-addon-border', 'forms');
                color: themed-component('input-group-addon-color', 'forms');
            }
        }
	    &.right-icon, &.left-icon {
		    .form-control {
			    border-radius: 4px;
			    & + .input-group-btn {
				    margin-left: -5px;
			    }
		    }
	    }
    }
    .invalid-feedback {
        color: themed-component('invalid-feedback-color', 'forms');
    }
}

