@import '../variables';

.checkbox-list {
    border: 1px solid rgba(0, 0, 0, 0.15);
	border-radius: 0.25rem;
    display: block;
    font-size: 1rem;
    height: 160px;
    line-height: 1.25;
    overflow-y: scroll;
    margin-bottom: 0.3rem;
    padding: 0.5rem 0.75rem;
    width: 100%;

    &:last-child {
        margin-bottom: 0.5rem;
    }

    .all {
        border-bottom: 1px solid $secondary;
        font-family: $font-family-book;
        margin-bottom: 7px;
        padding-bottom: 7px;
    }

    .filter {
        border-bottom: 1px solid $secondary;
        margin-bottom: 7px;
        padding-bottom: 7px;
    }

    label {
        color: inherit;
        display: flex;
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit;
    }
}
.dropdown-menu {
    padding: 0.25rem 0 0.5rem;

    .checkbox-list {
        border: 0;
        margin: -0.5rem 0;
        min-width: 180px;
    }
}
