@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.font-light {
    font-family: $font-family-light !important;
    font-weight: 300 !important;
}

a {
    text-decoration: none;
    transition: box-shadow ease-in-out 0.15s;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        @include set-focus;
    }
    &.nav-link {
        text-decoration: none;
    }

    @include themify($themes) {
        color: themed('href-color');
        &:hover {
            color: themed('href-hover-color');
        }
    }
}

p, label, li:not(.nav-link) {
    > a {
        text-decoration: underline;
    }
}

.arrow-bullets {
    list-style: none;
    padding-left: 0;
    > li {
        font-size: 1rem;
        line-height: calc-rem(24px);
        padding-left: 20px;
        position: relative;
        &:before {
            content: '\f04b';
            font-family: FontAwesome;
            font-size: calc-rem(10px);
            left: 0;
            line-height: calc-rem(10px);
            position: absolute;
            top: 8px;
        }
    }
}

h1, .h1 {
    font-family: $font-family-light;
    font-size: calc-rem(48px);
    font-weight: normal;
    line-height: calc-rem(55px);
    margin-bottom: 0;
    margin-top: 0;

    small, .small {
        margin: 0 6px 6px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

h2, .h2 {
    font-family: $font-family-book;
    font-size: calc-rem(20px);
    font-weight: normal;
    line-height: calc-rem(24px);
    margin-bottom: 0;
    margin-top: 0;

    small, .small {
        font-family: $font-family-light;
        margin: 0 3px 3px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

h3, .h3 {
    font-family: $font-family-light;
    font-size: calc-rem(18px);
    font-weight: normal;
    line-height: calc-rem(26px);
    margin-bottom: 0;
    margin-top: 0;
    text-transform: uppercase;
    &.large {
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }

    small, .small {
        margin: 0 5px 5px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

h4, .h4 {
    font-family: $font-family-book;
    font-size: 1rem;
    font-weight: normal;
    line-height: calc-rem(24px);
    margin-bottom: 0;
    margin-top: 0;

    small, .small {
        font-family: $font-family-light;
        margin: 0 5px 5px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

h5, .h5 {
    font-family: $font-family-book;
    font-size: calc-rem(14px);
    font-weight: normal;
    line-height: calc-rem(18px);
    margin-bottom: 0;
    margin-top: 0;

    small, .small {
        font-family: $font-family-light;
        margin: 0 4px 2px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

h6, .h6 {
    font-family: $font-family-light;
    font-size: calc-rem(14px);
    font-weight: normal;
    line-height: calc-rem(18px);
    margin-bottom: 0;
    margin-top: 0;

    small, .small {
        margin: 0 4px 2px;
    }

    &.text-monospace {
        font-family: $font-family-monospace;
    }
}

@include themify($themes, true) {
    h1, .h1 {
        color: themed-component('h1-color', 'typography');
    }
    h2, .h2 {
        color: themed-component('h2-color', 'typography');
    }
    h3, .h3 {
        color: themed-component('h3-color', 'typography');
    }
    h4, .h4 {
        color: themed-component('h4-color', 'typography');
    }
    h5, .h5 {
        color: themed-component('h5-color', 'typography');
    }
    h6, .h6 {
        color: themed-component('h6-color', 'typography');
    }
}

small,
.small {
    line-height: calc-rem(16px);
}

.p {
    text-transform: none;
    @include themify($themes) {
        color: themed-component('p-color', 'typography');
    }
}

strong, b {
    font-family: $font-family-book;
    font-weight: normal;
}

.monospace {
    font-family: $font-family-monospace;
}

.heading-box {
    margin-bottom: calc-rem(4px);
    padding: calc-rem(16px) calc-rem(16px) calc-rem(12px);
    position: relative;
    text-transform: uppercase;
    &:before {
        bottom: -4px;
        content: ' ';
        display: block;
        height: 1px;
        left: 0;
        position: absolute;
        width: 100%;
    }
    @include themify($themes) {
        background-color: themed-component('heading-box-background-color', 'typography');
        &:before {
            background-color: themed-component('heading-box-arrow-color', 'typography');
        }
    }
}
.heading-hrarw-ul {
    margin-bottom: 1rem;
    margin-top: 2rem;
    padding-bottom: calc-rem(8px);
    position: relative;
    &:after {
        border-style: solid;
        border-width: 4px 6px 0 6px;
        bottom: -6px;
        content: '';
        height: 0;
        left: 34px;
        position: absolute;
        width: 0;
    }
    @include themify($themes) {
        border-bottom: 2px solid themed-component('heading-hr-arrow-border-color', 'typography');
        &:after {
            border-color: themed-component('heading-hr-arrow-border-color', 'typography') transparent transparent transparent;
        }
    }
}

.heading-hrarw {
    font-size: 1.8rem;
    margin-bottom: 2rem;
    margin-top: 4rem;
    padding-bottom: calc-rem(16px);
    position: relative;
    text-align: center;
    &:after {
        border-style: solid;
        border-width: 4px 6px 0 6px;
        bottom: -6px;
        content: '';
        height: 0;
        left: 50%;
        margin-left: -3px;
        position: absolute;
        width: 0;
    }
    @include themify($themes) {
        border-bottom: 2px solid themed-component('heading-hr-arrow-border-color', 'typography');
        &:after {
            border-color: themed-component('heading-hr-arrow-border-color', 'typography') transparent transparent transparent;
        }
    }
}

.page-title {
    display: flex;
    flex-direction: row;
    height: 90px;
    padding: 0 20px 0 40px;
    width: 100%;
    .headings {
        align-self: center;
        display: flex;
        flex: 2;
        flex-direction: column;
        .header {
            font-family: $font-family-light;
            font-size: calc-rem(28px);
            font-weight: normal;
            line-height: calc-rem(34px);
        }
        .sub-heading {
            display: flex;
            flex-direction: row;
            font-family: $font-family-light;
            font-size: calc-rem(14px);
            font-weight: 400;
            list-style: none;
            .sub-heading-divider {
                align-self: center;
                height: 15px;
                justify-items: center;
                margin-left: 16px;
                margin-right: 16px;
                width: 1px;
            }
        }
    }
    .toolbox {
        align-self: center;
        display: flex;
        flex: 1;
        justify-content: flex-end;
        .btn {
            margin-left: 4px;
        }
    }

    @include themify($themes) {
        background-color: themed-component('page-title-background-color', 'typography');
        border-bottom: 1px solid themed-component('page-title-border', 'typography');
        .headings {
            .header {
                color: themed-component('page-title-header-color', 'typography');
            }
            .sub-heading {
                color: themed-component('page-title-subheading-color', 'typography');
                .sub-heading-divider {
                    background-color: themed-component('page-title-subheading-divider-color', 'typography');
                }
            }
        }
    }
}

.disabled {
    pointer-events: none;
}