@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.toggle-label {
    @include form-labels;
    display: block;
    & ~ .custom-toggle {
        display: inline-block;
    }
}

// Fixes icon alignment next to toggle.
.form-group {
    label {
        &.custom-control.custom-toggle {
            margin-bottom: 0;
        }
    }
    &.toggle-inline {
        display: flex;
        .toggle-label {
            line-height: 33px;
            margin-bottom: 0;
            margin-right: auto;
        }
        .custom-toggle {
            margin-left: 4px;
        }
    }
}

.custom-toggle {
    cursor: pointer;
    margin-bottom: 0;
    margin-right: 0;
    padding-left: 0;
    display: block;
    position: relative;

    .custom-control-input {
        min-height: 0;
        & ~ .custom-control-indicator {
            //border: 1px solid $alto;
            border-radius: 20px;
            cursor: pointer;
            display: inline-block;
            height: 24px;
            margin-top: 6px;
            pointer-events: auto;
            transition: background 0.4s, box-shadow ease-in-out 0.15s;
            user-select: none;
            outline: 0;
            width: 53px;
            &:after {
                //border: 1px solid darken($alto, 30%);
                border-radius: 20px;
                content: '';
                height: 20px;
                left: 2px;
                position: absolute;
                top: 8px;
                transition: margin 0.1s, background 0.4s;
                width: 20px;
                z-index: 5;
            }
        }
        & ~ .custom-control-description {
            bottom: 2px;
            font-size: 11px;
            font-weight: normal;
            left: 0;
            line-height: 16px;
            position: absolute;
            right: 0;
            top: 10px;
            vertical-align: top;
            transition: left 0.2s;
            width: 20px;
            .toggle-on {
                display: block;
                left: 8px;
                opacity: 0;
                position: absolute;
                transition: opacity 0.2s;
                white-space: nowrap;
            }
            .toggle-off {
                display: block;
                left: 26px;
                opacity: 1;
                position: absolute;
                text-align: right;
                transition: opacity 0.2s;
                width: 19px;
                white-space: nowrap;
            }
        }
        &:checked {
            & +.custom-control-indicator {
                font-weight: normal;
                &:after {
                    margin-left: 29px;
                }
            }
            & ~.custom-control-description {
                .toggle-on {
                    opacity: 1;
                }
                .toggle-off {
                    opacity: 0;
                }
            }
        }
        &:disabled {
            cursor: not-allowed !important;
            & ~ .custom-control-indicator {
                cursor: not-allowed;
                opacity: 0.5;
            }
            & ~ .custom-control-description {
                cursor: not-allowed;
                opacity: 0.5;
            }
        }
        &:focus {
            & ~ .custom-control-indicator {
                @include set-focus;
            }
        }
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .custom-control-input {
            & ~ .custom-control-indicator {
                background-color: themed-component('toggle-background-color', 'toggle');
                box-shadow: themed-component('toggle-box-shadow', 'toggle');
                &:after {
                    background-color: themed-component('toggle-after-background-color', 'toggle');
                    box-shadow: themed-component('toggle-after-box-shadow', 'toggle');
                }
            }
            & ~ .custom-control-description {
                .toggle-off {
                    color: themed-component('toggle-off-color', 'toggle');
                }
            }
            &:checked {
                & + .custom-control-indicator {
                    color: themed-component('toggle-checked-color', 'toggle');
                }
                & ~.custom-control-description {
                    color: themed-component('toggle-checked-description-color', 'toggle');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}

//Apply alternate colors to an active toggle
@each $name, $color in $all-colors {

    .custom-toggle {
        .custom-control-input {
            &:checked.toggle-#{$name} {
                & ~ .custom-control-indicator {
                    @include themify($themes) {
                        $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                        background-color: $background-color;
                        border-color: $background-color;
                        @if $name == 'default' {
                            background-color: themed-component('toggle-default-background-color', 'toggle');
                            border-color: themed-component('toggle-default-background-color', 'toggle');
                        }
                    }
                    &:focus {
                        @include set-focus;
                        //box-shadow: 0 0 0 3px $gallery, inset 0 0 2px $border-color;
                    }
                }
	            & ~.custom-control-description {
		            @include themify($themes) {
			            $background-color: themed-component(#{$name + '-background-color'}, 'colors');
			            $color: test-contrast($background-color);
			            @if $name == 'default' {
				            $color: themed-component('toggle-checked-description-color', 'toggle');
			            }
			            color: $color;
		            }
	            }
            }
        }
    }
}
