@import '../variables';
@import '../mixins/mixins';

.custom-checkbox {
    //align-items: center;
    cursor: pointer;
    display: flex;
    margin-bottom: 0;
    padding: 2px 0;

    .custom-control-input {
        & ~ .custom-control-indicator {
            /*background-color: $white;
            border: 1px solid $alto;*/
            display: inline-block;
            height: 16px;
            opacity: 1;
            pointer-events: auto;
            width: 16px;
            transition: box-shadow ease-in-out 0.15s;

            &:after {
                // color: $black;
                content: '\f00c';
                display: block;
                font-family: FontAwesome;
                font-size: 12px;
                height: 15px;
                line-height: 15px;
                text-align: center;
                transition: all 0.2s;
                vertical-align: middle;
                width: 15px;
            }
            &:focus {
                @include set-focus;
            }
        }
        & ~ .custom-control-description {
            margin-left: 8px;
            margin-top: -4.5px;
        }

        &:checked {
            & ~ .custom-control-indicator {
                //background: $white none;
                //border: 1px solid $black;

                &:after {
                    opacity: 1;
                    transform: scale(1);
                }

                &:disabled {
                    //background-color: $white;
                    //border-color: #bbbbbb;
                    box-shadow: none;

                    & ~ .custom-control-description {
                        //color: #aaaaaa;
                        opacity: 0.7;
                    }
                }
            }
        }

        &:not(:checked) {
            & ~ .custom-control-indicator {
                &:after {
                    opacity: 0;
                    transform: scale(0);
                }
            }
        }

        &:disabled {
            cursor: not-allowed;
            & ~ .custom-control-indicator {
                //background-color: $white;
                box-shadow: none;
                //color: #aaaaaa;
                cursor: not-allowed;
                opacity: 0.7;

                &:after {
                    //color: #999999;
                }
            }

            & ~ .custom-control-description {
                //color: #aaaaaa;
                cursor: not-allowed;
                opacity: 0.7;
            }
        }
        &:focus {
            & ~ .custom-control-indicator {
                @include set-focus;
            }
        }
    }

    @include themify($themes) {
        .custom-control-input {
            & ~ .custom-control-indicator {
                background-color: themed-component('checkbox-background-color', 'forms');
                border: 1px solid themed-component('checkbox-border-color', 'forms');

                &:after {
                    color: themed-component('checkbox-check-color', 'forms');
                }
            }
	        & ~ .custom-control-description {
		        color: themed-component('checkbox-color', 'forms');
	        }

            &:checked {
                & ~ .custom-control-indicator {
                    background: themed-component('checkbox-checked-background-color', 'forms') none;
                    border: 1px solid themed-component('checkbox-checked-border-color', 'forms');

                    &:disabled {
                        background-color: themed-component('checkbox-background-color', 'forms');
                    }
                }
            }

            &:disabled {
                cursor: not-allowed;

                & ~ .custom-control-indicator {
                    background-color: themed-component('checkbox-background-color', 'forms');
                }

                & ~ .custom-control-description {
                    color: themed-component('checkbox-disabled-color', 'forms');
                }
            }
        }
    }
}

//Apply alternate colors to an active checkbox
@each $name, $color in $action-colors {
    @include themify($themes, true) {
        $background-color: themed-component('checkbox-background-color', 'forms');
        $border-color: themed-component(#{$name + '-border-color'}, 'colors');
        $color: themed-component(#{$name + '-color'}, 'colors');

        @if $name == 'default' {
            $border-color: themed-component('gray-border-color', 'colors');
            $color: themed-component('gray-border-color', 'colors');
        }

        .custom-checkbox {
            .custom-control-input {
                & ~ .custom-control-indicator {
                    background-color: $background-color;
                }

                &:checked.checkbox-#{$name} {
                    & ~ .custom-control-indicator {
                        border-color: $border-color;

                        &:after {
                            color: $color;
                        }
                    }
                }
                &.is-invalid {
                    & ~ .custom-control-indicator {
                        border: 1px solid themed-component('invalid-feedback-color', 'forms');
                    }
                    &:checked {
                        & ~ .custom-control-indicator {
                            border: 1px solid themed-component('invalid-feedback-color', 'forms');
                            &:after {
                                color: themed-component('invalid-feedback-color', 'forms');
                            }
                        }
                    }
                }
            }
        }
    }
}


fieldset {
    margin-bottom: 0.5rem;
    .custom-checkbox {
        & ~ .invalid-feedback {
            display: block;
        }
    }

    & clui-checkbox {
        & ~ .invalid-feedback {
            display: block;
        }
    }
}

.custom-control-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .custom-checkbox {
        margin-right: 8px;
    }
}

.custom-radio .custom-control-input:checked.radio-default ~ .custom-control-indicator:focus {
    box-shadow: none;
}
