@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.custom-file {
    position: relative;
    .custom-file-input {
        cursor: pointer;
        pointer-events: auto;
        position: absolute;
        z-index: 1;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        .custom-file-input {
            &:focus {
                ~ .custom-file-control {
                    box-shadow: themed-component('focus-drop-shadow', 'file-browser');
                }
            }
        }
    }
    //- Theming ========================================================================================================
}