@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.nav-tabs {
    @include themify($themes) {
        border-bottom: themed-component('border-bottom', 'nav-tabs');
    }
    .nav-link.active,
    .nav-item.show .nav-link {
        font-family: $font-family-book;
        @include themify($themes) {
            background-color: themed-component('nav-link-background-color', 'nav-tabs');
            border-color: themed-component('nav-link-border-color', 'nav-tabs');
            color: themed-component('nav-link-color', 'nav-tabs');
            &:hover, &:focus {
                background-color: themed-component('nav-link-background-color', 'nav-tabs');
                border-color: themed-component('nav-link-border-color', 'nav-tabs');
            }
        }
    }
    .nav-link {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        font-family: $font-family-book;
        padding: 0.75rem calc-rem(20px) 0.625rem;
        position: relative;
        text-decoration: none;
        &:hover, &:focus {
            @include themify($themes) {
                //background-color: themed-component('hover-background-color', 'nav-tabs');
                border-color: themed-component('nav-link-hover-border-color', 'nav-tabs');
                background-color: themed-component('nav-link-hover-background-color', 'nav-tabs');
            }
        }
    }
}