@import '../variables';
@import '../mixins/mixins';
@import '../themes';
@import '../functions';

$avatar-sizes: ('lg', 'md', 'sm', 'xs');
$start-size: 100px;

@mixin avatar-variant($size: 60px) {
    font-size: $size / 2;
    height: $size - 2;
    line-height: $size - 2;
    min-height: $size - 2;
    min-width: $size;
    width: $size;

}

.avatar {
    @include avatar-variant;
    align-items: center;
    background-position-x: center;
    background-position-y: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    margin-right: 1rem;
    text-align: center;

    @include themify($themes) {
        background-color: themed-component('background-color', 'avatars');
        box-shadow: themed-component('box-shadow', 'avatars');
        color: themed-component('color', 'avatars');
    }

    @each $avt in $avatar-sizes {
        $i: index($avatar-sizes, $avt);
        &.avatar-#{$avt} {
            @include avatar-variant($start-size - (20 * $i));
        }
    }

    $merged: map-merge($all-colors, $theme-colors);
	$mergedAgain: map-merge($merged, $new-brand-colors19);


    @each $name, $color in $mergedAgain {
        @include themify($themes, true) {

            &.bg-#{$name} {
                $background-color: themed-component(#{$name + '-background-color'}, 'colors');
                background-color: $background-color;

                @if index('orange' 'orange-dark' 'warning',$name) { color: $cl-blue-dark; }
                @else { color: test-contrast($background-color); }
            }
        }
    }

    &.avatar-xl {
        @include avatar-variant(140px);
    }

    &.photo {
        > .monospace {
            display: none;
        }
    }
}

