@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.awesomplete {
    display: flex;
    position: relative;
    width: 100%;

    [hidden] {
        display: none;
    }

    .visually-hidden {
        clip: rect(0, 0, 0, 0);
        position: absolute;
    }

    > input {
        display: block;
    }

    > ul {
        background-clip: padding-box;
        border-radius: 0 0 0.25rem 0.25rem;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        left: 0;
        list-style: none;
        margin-bottom: 0;
        min-width: 100%;
        padding: 0.5rem 0;
        position: absolute;
        text-shadow: none;
        top: 38px;
        width: 100%;
        z-index: 9999;
        &:empty {
            display: none;
        }
        > li {
            @include font-size(14px);
            border: 0;
            clear: both;
            cursor: pointer;
            display: block;
            font-weight: normal;
            line-height: 21.6px;
            padding: 0.25rem 1.5rem;
            position: relative;
            text-align: inherit;
            width: 100%;
            .subcategory {
                @include font-size(12px);
                line-height: 21px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .category {
                @include font-size(12px);
                cursor: default;
                font-weight: bold;
                margin: 0 -1.5rem 2px;
                padding: 8px 1.5rem 6px;
                text-transform: uppercase;
                user-select: none;
            }
            &.with-category {
                padding-top: 0;
                &:first-child {
                    border-top: 0;
                    margin-top: -10px;
                }
            }
            &:first-child {
                .category {
                    border-top: 0;
                }
            }
        }
    }

    mark {
        font-weight: bold;
        padding: 5px 0;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        > ul {
            background-color: themed-component('background-color', 'awesomplete');
            border: themed-component('border', 'awesomplete');
            box-shadow: themed-component('box-shadow', 'awesomplete');
            > li {
                background-color: themed-component('background-color', 'awesomplete');
                color: themed-component('color', 'awesomplete');
                .subcategory {
                    color: themed-component('category-color', 'awesomplete');
                }
                .category {
                    background-color: themed-component('category-background-color', 'awesomplete');
                    border-top: themed-component('category-border-top', 'awesomplete');
                    color: themed-component('category-color', 'awesomplete');
                }
                &.with-category {
                    border-top: themed-component('with-category-color', 'awesomplete');
                }
                &:hover {
                    background-color: themed-component('hover-background-color', 'awesomplete');
                }
                &[aria-selected='true'] {
                    background-color: themed-component('hover-background-color', 'awesomplete');
                }
            }
        }
        mark {
            background: themed-component('mark-background', 'awesomplete');
            color: themed-component('mark-color', 'awesomplete');
        }

    }
    //- Theming ========================================================================================================
}

@supports (transform: scale(0)) {
    .awesomplete > ul {
        transform-origin: 1.43em -0.43em;
        transition: 0.3s cubic-bezier(0.4, 0.2, 0.5, 1.4);
        &[hidden], &:empty {
            display: block;
            transform: scale(0);
            transition-timing-function: ease;
        }
        //- Theming ========================================================================================================
        @include themify($themes) {
            &[hidden], &:empty {
                opacity: themed-component('opacity', 'awesomplete');
            }
        }
        //- Theming ========================================================================================================
    }
}