@import '../variables';

.meta-table {
    .card {
        margin-bottom: -1px;
        transition: margin 0.3s ease-in-out;
        .card-header {
            min-height: 65px;
            transition: background-color 0.3s ease-in-out;
            .collapse-heading {
                font-size: calc-rem(20px);
                padding-left: 20px;
                transition: font-size 0.3s;
                &:before {
                    font-size: calc-rem(20px);
                }
            }
            [class^='alert-'] {
                height: 67px;
            }
        }
        .card-body {
            padding: calc-rem(40px);
        }
        .footer-pages {
            align-items: center;
            display: flex;
            font-family: $font-family-book;
            font-size: 1rem;
        }
        &.open {
            //box-shadow: 0 0 0 7px rgba($gray, 0.25);
            margin-bottom: 15px;
            margin-top: 14px;
            //border-color: $primary;
            .card-header {
                .collapse-heading {
                    font-size: calc-rem(22px);
                    &:before {
                        font-size: calc-rem(22px);
                    }
                }
            }
            .card-body {
                .table {
                    //box-shadow: 0 0 4px 0 rgba($black, 0.55);
                    margin-bottom: 0;
                }
            }
            .card-footer {
                background-color: $seashell;
            }
        }
    }
}

.metatable {
    .card.open .card {
        .card-header {
            border-bottom: 1px solid $alto;
        }
    }

    .card {
        border-radius: 0;
        box-shadow: none;
        margin: 0 auto;
        transform-origin: center top;
        transition: width 0.2s ease-in-out, background 0.2s ease-in-out, margin 0.2s ease-in-out;
        will-change: transform;

        &.open {
            background: $alto;
            border: 0;
            box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.13);
            margin-bottom: 30px;
            margin-left: -30px;
            margin-top: 30px;
            width: calc(100% + 60px);
            will-change: initial;

            .card-body {
                border: 0;
                padding: 0 30px 30px;

                .card-body {
                    padding: 21px 30px;
                }
            }

            .card-header {
                font-size: calc-rem(30px);
                width: calc(100% + 60px);
            }
        }

        .card-header {
            border-bottom: 0;
            justify-content: center;
            line-height: 45px;
            min-height: 75px;
            padding: 19px 30px;

            &[data-toggle='collapse'] {
                .icon {
                    padding-right: 30px;
                }
            }

            &.collapsing {
                width: 100%;
            }
        }
    }

    .card + .card:not(.open) {
        &:last-of-type {
            &:before {
                border-radius: 0 0 $border-radius-base $border-radius-base;
            }
        }
    }

    .card:first-of-type {
        &:before {
            border-top-left-radius: $border-radius-base;
            border-top-right-radius: $border-radius-base;
        }
    }

    .card.open ~ .card, .card:not(.open) {
        position: relative;
        &:before {
            bottom: 0;
            box-shadow: 3px 0 3px 0 rgba(0, 0, 0, 0.13),
            -3px 0 3px 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13);
            content: '';
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            z-index: -1;
        }

        .card:first-of-type {
            border-top-left-radius: $border-radius-base;
            border-top-right-radius: $border-radius-base;
            box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13),
            0 -3px 3px 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13);
        }

        .card:last-of-type {
            border-bottom-left-radius: $border-radius-base;
            border-bottom-right-radius: $border-radius-base;
            box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13),
            0 0 0 0 rgba(0, 0, 0, 0.13),
            0 3px 3px 0 rgba(0, 0, 0, 0.13);
        }
    }

    //border radius for the card below an open card
    .card.open + .card {
        border: 0;
        border-radius: $border-radius-base $border-radius-base 0 0;

        &:before {
            border-radius: $border-radius-base $border-radius-base 0 0;
        }
    }

    //border radius for the card above an open card
    .card.card-above:before {
        border-bottom-left-radius: $border-radius-base;
        border-bottom-right-radius: $border-radius-base;
    }

    .card + .card {
        border-top: 1px solid $alto;
        margin-top: 0;
    }

    .card:not(.open) {
        width: 100%;
    }

    &:first-child.open {
        margin-top: 0;
    }

    &:last-child.open {
        margin-bottom: 0;
    }
}