@mixin sel-table-cells {
    > thead, > tbody {
        > tr {
            > th, > td {
                @content;
            }
        }
    }
}
@mixin sel-thead-tr {
    > tr {
        > th {
            @content;
        }
    }
}
@mixin sel-thead-th {
    > thead {
        > tr {
            > th {
                @content;
            }
        }
    }
}
@mixin sel-tbody-tr {
    > tr {
        > td {
            @content;
        }
    }
}
@mixin sel-tbody-td {
    > tbody {
        > tr {
            > td {
                @content;
            }
        }
    }
}