@import '../variables';
@import '../mixins/mixins';

.text-collapse {
    .text-collapse-sub-section {
        color: $clBlack;
        font-family: $font-family-book;
        line-height: calc-rem(55px);
        padding: 12px 30px;
        position: relative;
        &:before {
            color: $primary;
            content: '\f107';
            font-family: FontAwesome;
            font-size: calc-rem(22px);
            left: 10px;
            line-height: 10px;
            margin-top: 0;
            position: absolute;
            top: 38%;
            transition: transform 300ms ease-in-out;
            width: 14px;
        }
        &.collapsed {
            &:before {
                transform: rotate(-90deg);
                transition: transform 300ms ease-in-out;
            }
        }
    }
    p {
        margin-left: 2rem;
        margin-right: 2rem;
    }
}