@import '../variables';
@import '../mixins/mixins';
@import '../themes';

html {
    min-height: 100%;
    position: relative;
}

body {
    -moz-osx-font-smoothing: grayscale !important;
    -webkit-font-smoothing: antialiased !important;
    font-family: $font-family-light;
    font-size: calc-rem(16px);
    font-weight: normal;
    letter-spacing: -0.015em;
    line-height: calc-rem(26px);
    text-rendering: optimizeLegibility !important;

    @include themify($themes) {
        background-color: themed-component('background-color', 'body');
        color: themed-component('color', 'body');
	    &.body-gray-bg {
		    background-color: themed-component('background-color-gray', 'body');
	    }
    }
}

$merged: map-merge($all-colors, $theme-colors);
$merged: map-merge($merged, $gray-colors);
$merged: map-merge($merged, $new-brand-colors19);

@each $name, $color in $merged {
    @include themify($themes, true) {
        .text-#{$name} {
            color: themed-component(#{$name + '-color'}, 'colors') !important;
        }
        .bg-#{$name} {
            background-color: themed-component(#{$name + '-background-color'}, 'colors') !important;
        }
        .border-#{$name} {
            border: themed-component(#{$name + '-border-color'}, 'colors') !important;
        }
    }
}

.text-cl-black {
    color: $cl-black !important;
}
.bg-cl-black {
    background-color: $cl-black !important;
}
.border-cl-black {
    border: $cl-black !important;
}

.bg-gallery-30 {
    background-color: rgba($gallery, 0.3);
}

.border, .border-top, .border-right, .border-bottom, .border-left {
    @include themify($themes) {
        border-color: themed('border-color');
    }
}

@mixin clgx-logo-color {
    @include themify($themes) {
        background: themed-component('clgx-logo-color', 'logo');
        background-size: 53px 43px;
    }
}

@mixin clgx-logo-white {
    @include themify($themes) {
        background: themed-component('clgx-logo-white', 'logo');
        background-size: 53px 43px;
    }
}

.iframe-container-fill {
    align-self: stretch;
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    iframe {
        flex: 1 1 auto;
    }
}

hr {
    @include themify($themes) {
        border-top: 1px solid themed-component('hr-border-color', 'body');
        &.down-arrow {
            border-top: 2px solid themed-component('hr-arrow-color', 'body');
            &:after {
                border-color: themed-component('hr-arrow-color', 'body') transparent transparent transparent;
                border-style: solid;
                border-width: 4px 6px 0 6px;
                content: '';
                height: 0;
                left: 50%;
                margin-left: -3px;
                position: absolute;
                width: 0;
            }
        }
    }
}
