@import '../variables';
@import '../mixins/mixins';
@import '../themes';

.tooltip {
    font-family: $font-family-book;
    font-size: 13px;
    line-height: 13px;
    // top: 0 fixes the off-centered tooltips when added to the body.
    top: 0;
    .tooltip-inner {
        border-radius: 0;
        padding: 6px 6px 5px;
    }
    //- Theming ========================================================================================================
    @include themify($themes) {
        &.show {
            opacity: themed-component('opacity', 'tooltips');
        }
        .tooltip-inner {
            background-color: themed-component('background-color', 'tooltips');
            color: themed-component('color', 'tooltips');
            box-shadow: themed-component('box-shadow', 'tooltips');
        }
        &.bs-tooltip-left .arrow::before, &.bs-tooltip-auto[x-placement^='left'] .arrow::before {
            border-left-color: themed-component('background-color', 'tooltips');
        }

        &.bs-tooltip-right .arrow::before, &.bs-tooltip-auto[x-placement^='right'] .arrow::before {
            border-right-color: themed-component('background-color', 'tooltips');
        }

        &.bs-tooltip-top .arrow::before, &.bs-tooltip-auto[x-placement^='top'] .arrow::before {
            border-top-color: themed-component('background-color', 'tooltips');
        }

        &.bs-tooltip-bottom .arrow::before, &.bs-tooltip-auto[x-placement^='bottom'] .arrow::before {
            border-bottom-color: themed-component('background-color', 'tooltips');
        }
    }
    //- Theming ========================================================================================================
}
